import React from 'react'
import { Button, Grid, TextField, makeStyles, useMediaQuery, useTheme } from '@material-ui/core'
import sp_logo from '../assets/sp_logo.png';

const useStyles = makeStyles((theme) => ({
    shopifyIntegratesMain: {
        margin: '60px 8.15% 0 8.15%',
        background: "#1F202F",
        textAlign: 'center',
        [theme.breakpoints.down('sm')]: {
            margin: '30px 4.15% 0 4.15%',
        }
    },
    shopifyIntegratesHead: {
        background: '#5A863E',
        textAlign: 'center',
        padding: '35px',
        [theme.breakpoints.down('sm')]: {
            padding: '15px',
        }
    },
    shopifyIntegratesForm: {
        padding: '60px',
        textAlign: 'left',
        [theme.breakpoints.down('sm')]: {
            padding: '20px',
        }
    },
    shopifyformHead: {
        color: '#FFFFFF',
        fontSize: '24px',
        fontWeight: 500,
        lineHeight: '26px',
        [theme.breakpoints.down('sm')]: {
            fontSize: '16px',
            fontWeight: 500,
            lineHeight: '16px',
            paddingBottom: '0'
        }
    },
    shopifyEndMenu: {
        color: '#8D99AE',
        fontSize: '20px',
        fontWeight: 600,
        lineHeight: '24px',
        [theme.breakpoints.down('sm')]: {
            fontSize: '7.14px',
            fontWeight: 600,
            lineHeight: '8.57px',
        }
    },
    shopifyIntegratesBtn: {
        background: "#555768", padding: '22px',
        color: '#F4F5F7',
        fontSize: '24px',
        lineHeight: '24px',
        fontWeight: 600,
        textTransform: 'none',
        [theme.breakpoints.down('sm')]: {
            fontSize: '18px',
            fontWeight: 500,
            lineHeight: '20px',
        }
    },
    inviteFormTextField: {
        marginBottom: '20px',
        '& .MuiOutlinedInput-root': {
            borderRadius: '20px 0 0 20px',
            [theme.breakpoints.down('sm')]: {
                borderRadius: '20px',
                width: '100%',
                fontSize: '16px',
                lineHeight: '18px',
                fontWeight: 500,
            },
            '& fieldset': {
                borderWidth: '2px',
                borderColor: '#808B9E',
            },
            '&:hover fieldset': {
                borderColor: '#808B9E',
            },
            '&.Mui-focused fieldset': {
                borderColor: '#808B9E',
            },
        },        
    },
    verifyShopifyBtn: {
        background: '#F4F5F7',
        fontSize: '22px',
        fontWeight: 600,
        lineHeight: "26px",
        padding: '21px',
        borderRadius: '0 20px 20px 0',
        textTransform: 'none',
        [theme.breakpoints.down('sm')]: {
            borderRadius: '20px',
            fontSize: '14px',
            lineHeight: "16px",
        },
    },
    shopifyNoteSection: {
        display: 'flex', alignItems: 'center'
    },
    shopifyNoteIcon:{
        color: '#FFFFFF', background: '#8D99AE', width: '62px', height: '44px', borderRadius: '30px', display: 'flex', justifyContent: 'center', alignItems: 'center' ,
        [theme.breakpoints.down('sm')]:{
            width: '45px', height: '24px', borderRadius: '20px',fontSize:'9px'
        },
      },
    shopifyNoteText: {
        marginLeft: '20px', color: '#646D7C',
        fontSize: '18px',
        fontWeight: 400,
        lineHeight: '20px',
        [theme.breakpoints.down('sm')]: {
            fontSize: '10px',
            lineHeight: '16px',
            fontWeight: 500,
        },
    },
    shopifyLogoSize: {
        [theme.breakpoints.down('sm')]: {
            width: "84px",
            height: "24px"
        },
    }
}))
function ShopifyIntegrates() {
    const classes = useStyles()
    const theme = useTheme()
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'))
    return (
        <div className={classes.shopifyIntegratesMain}>
            <div className={classes.shopifyIntegratesHead}>
                <img src={sp_logo} className={classes.shopifyLogoSize} />
            </div>
            <div className={classes.shopifyIntegratesForm}>
                <Grid container spacing={4}>
                    <Grid item xs={12}>
                        <span className={classes.shopifyformHead}>Store Name Or Store URL </span>
                    </Grid>
                    <Grid item md={8} xs={12}>
                        <TextField
                            placeholder="www.apple.com"
                            variant="outlined"
                            name='Store Name'
                            type="text"
                            fullWidth
                            className={classes.inviteFormTextField}
                            inputProps={{
                                style: {
                                    padding: !isMobile ? '20px 40px' :'18px 22px',
                                    color: '#EAEAEC',
                                    fontSize: !isMobile ?'24px':'16px',
                                    fontWeight: 500,
                                    lineHeight:!isMobile ? '26px':'18px',
                                }
                            }}
                        />
                    </Grid>
                    <Grid item md={4} xs={12}>
                        <Button fullWidth className={classes.verifyShopifyBtn}>Verify</Button>
                    </Grid>
                    <Grid className={classes.shopifyNoteSection} item xs={12}>
                        <span className={classes.shopifyNoteIcon}>Note</span>
                        <span className={classes.shopifyNoteText}>This will crawl all the links starting with the URL (not including files on the website).</span>
                    </Grid>
                    <Grid item xs={12}>
                        <Button fullWidth className={classes.shopifyIntegratesBtn}>Login</Button>
                    </Grid>
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        <span className={classes.shopifyEndMenu}>Terms And Conditions </span>
                    </Grid>
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        <span className={classes.shopifyEndMenu}>Cache and Cookie </span>
                    </Grid>
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        <span className={classes.shopifyEndMenu}>Privacy policy </span>
                    </Grid>
                </Grid>
            </div>
        </div>
    )
}

export default ShopifyIntegrates