import { Grid, makeStyles } from '@material-ui/core';
import React from 'react';
import Chatdemo from '../components/HomePage/ChatDemo';
import loop_Logo from '../assets/Loop 4.png';
import ChatBox from '../components/ChatBox';
import ChatBoxIframe from '../components/ChatBoxIframe';

const useStyles = makeStyles((theme) => ({
    previewMainContainer: {
        background: theme.palette.common.black,
        padding: '60px 8.15% 200px 8.15%',
        [theme.breakpoints.down('sm')]: {
            padding: '30px 4.15% 100px 4.15%',
        }
    },
    previewHeadText: {
        fontSize: '60px',
        fontWeight: 500,
        lineHeight: '64px',
        color: '#FFFFFF',
        [theme.breakpoints.down('sm')]: {
            fontSize: '24px',
            lineHeight: '26px',
        }
    },
    previewText: {
        fontSize: '22px',
        fontWeight: 500,
        lineHeight: '38px',
        color: '#717280',
        margin: '30px 0',
        wordWrap: 'break-word',
        [theme.breakpoints.down('sm')]: {
            fontSize: '12px',
            lineHeight: '16px',
        }
    },
    previewLoop: {
        marginLeft: '-100px',
        width: '121px',
        height: '63px',
        [theme.breakpoints.down('sm')]: {
            width: '51px',
            height: '27px',
            marginLeft: '-50px',
        }
    }
}));

function Preview() {
    const classes = useStyles();
    return (
        <Grid className={classes.previewMainContainer}>
            <Grid item xs={12}>
                <div className={classes.previewHeadText}>Preview <img src={loop_Logo} className={classes.previewLoop} alt='loop'/></div>
                <p className={classes.previewText}>
                    This chatbot was trained on a document explaining UrbanChat, You can embed a widget like this on any page on your website!
                </p>
            </Grid>
            <Grid item xs={12}>
            <ChatBoxIframe defaultId={"ba5d93b7-39cc-439f-8e0b-f7d833c394d7"}/>
            </Grid>
        </Grid>
    );
}

export default Preview;