import React, { useContext, useEffect, useRef, useState } from "react";

import eatImg from "../../../assets/eat.png";
import chatIcon from "../../../assets/Icons/chat.svg";
import Dropdown from "../../Widget/Dropdown";
import Slider from "../../Widget/Slider";
import { ServerDataContext } from "../../../pages/MyChatbot/UpdateChatbot";
import { updateChatbotById } from "../../../lib/api/ApiUpdateChatbot";
import { toast } from "react-hot-toast";
import { useParams } from "react-router-dom";

export default function Model() {
  const { serverData, setServerData } = useContext(ServerDataContext)
  const [progress, setProgress] = useState(0);
  const [isSaving, setIsSaving] = useState(false)
  const { id } = useParams();



  const formData = useRef({
    base_prompt: '',
    temperature: 0,
    chatgpt_model_type: ''
  })



  function onModelSelect(value) {
    console.log(value)
    formData.current.chatgpt_model_type = value
  }


  useEffect(() => {
    setProgress(serverData.chatbot_model.temperature)

  }, [])



  async function onSave() {
    if(isSaving) return
    const data = structuredClone(formData.current)

    // filter
    if (data.base_prompt == serverData.chatbot_model.base_prompt) delete data.base_prompt
    if (data.chatgpt_model_type == serverData.chatbot_model.chatgpt_model_type) delete data.chatgpt_model_type
    if (data.temperature == serverData.chatbot_model.temperature) delete data.temperature

    // do more ...


    if (Object.keys(data).length == 0) {
      toast.error('Please update something first')
      return
    }

    setIsSaving(true)
    const res = await updateChatbotById(id, data)
    if (res.data) {
      toast.success('Updated successfully')
      setServerData((old) => {
        const raw = structuredClone(old)
        if (data.base_prompt) raw.chatbot_model.base_prompt = data.base_prompt
        if (data.chatgpt_model_type) raw.chatbot_model.chatgpt_model_type = data.chatgpt_model_type
        if (data.temperature) raw.chatbot_model.temperature = data.temperature

        return raw
      })
    } else {
      toast.error(res.message)
    }

    setIsSaving(false)
  }

  useEffect(() => {

    // set state
    formData.current.base_prompt = serverData.chatbot_model.base_prompt
    formData.current.chatgpt_model_type = serverData.chatbot_model.chatgpt_model_type
    formData.current.temperature = serverData.chatbot_model.temperature

  }, []);

  return (
    <div>
      <div>
        <h2>Model</h2>

        <p className="input-title">Base Prompt (system message)</p>
        <textarea onChange={(e) => formData.current.base_prompt = e.target.value} defaultValue={serverData.chatbot_model.base_prompt} rows={6} placeholder="Enter message"></textarea>
        <div className="clean-all">
          <p className="note">Enter each message in a new line.</p>
          <button onClick={(e) => alert(e.target.parent.previousSibling.value)}>Clean All</button>
        </div>

        <p className="input-title">Chatbot Model</p>
        <div style={{ maxWidth: 600 }}>
          <Dropdown
            items={["gpt-3.5-turbo", 'gpt-4-turbo']}
            value={serverData.chatbot_model.chatgpt_model_type}
            onSelect={onModelSelect}
          />
        </div>
        <p className="note">Enter each message in a new line.</p>

        <div style={{ maxWidth: 600 }}>
          <div className="temperature">
            <p className="input-title">Temperature</p>
            <span>{progress}%</span>
          </div>
          <Slider
            texts={["Reserved", "Creative"]}
            value={progress}
            onProgress={(e) => {
              const temp = parseInt(e)
              formData.current.temperature = temp
              setProgress(temp)
            }}
          />
        </div>

        <div className="button-holder">
          <button className="secondary">Previous</button>
          <button onClick={onSave} className="primary">{isSaving? (<span className="loading small"></span>) : "Save & Next"}</button>
        </div>
      </div>
    </div>
  );
}
