import React from "react";
import '../../styles/HomePage/landing.css'
// import '../../styles/page.css'
import robotImage from '../../assets/robot.png';
import chatImage from '../../assets/chat-img.svg';
import gradient from '../../assets/gradiant.svg';
import { Link } from "react-router-dom";


export default function Landing() {
    return <section className="landing">
        <div className="page">
            <div className="left">
                <h1>The Ultimate Ai Chatbot <span>Solution</span></h1>
                <p>Elevate your Customer Engagement with an intelligent AI Chatbot. Seamlessly integrate AI Customer Support across platforms. Give your customers a personalized and responsive 24/7 customer support with a chat interface, expertly crafted and tailored from your data.</p>
                <Link to="/login" className="hover"><button>Join waiting list <svg width="24" height="24" fill="none" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="M13.704 4.284a1 1 0 1 0-1.403 1.424L17.67 11H4a1 1 0 1 0 0 2h13.665L12.3 18.285a1 1 0 0 0 1.403 1.424l6.925-6.822a1.25 1.25 0 0 0 0-1.78l-6.925-6.823Z" fill="#fff" /></svg></button></Link>
            </div>
            <div className="right">
                <div className="desktop">
                    <img className="bot" src={robotImage} />
                    <img className="gradient" src={gradient} />
                </div>
                <img className="bot" src={chatImage} />
                <div className="phone">
                    <img className="bot" src={robotImage} />
                    <img className="gradient" src={gradient} />
                </div>
            </div>
        </div>
    </section>
}