import React, { useState, useEffect } from "react";

import FileUpload from "../../Widget/FileUpload";
import {
  createChatSourceByFile,
  createChatbot,
} from "../../../lib/api/ApiCreateChatbot";
import toast from "react-hot-toast";

import fileIcon from "../../../assets/Icons/FileBlankIcon.svg";
import "../../../styles/CreateChatbot/files.css";
import { useHistory } from "react-router-dom";
import { useParams } from "react-router-dom";
import { updateChatSource } from "../../../lib/api/ApiUpdateChatbot";

export default function Files({ isClear, setIsClear, isEditMode }) {
  // store array of files
  const [files, setFiles] = useState([]);
  const [isProcessing, setIsProcessing] = useState(false);

  const history = useHistory()
  const { id } = useParams();


  async function getChatbotId() {
    const res = await createChatbot();
    if (res.data) {
      // toast.success('id created')
      return res.data.chatbot_id;
    } else {
      toast.error(res.message);
      return null;
    }
  }

  async function onSubmit() {
    if (isProcessing) return;
    if (files.length == 0) {
      toast("Please select file first", {
        icon: "⚠",
      });
      return;
    }

    // get chatbot id
    setIsProcessing(true);

    const form = new FormData();
    files.forEach((element) => {
      form.append(element.name, element);
    });

    if (isEditMode) {
      await createChatSource(id, form);
    } else {
      const chatbotId = await getChatbotId();
      if (!chatbotId) {
        setIsProcessing(false);
        return;
      }

      await createChatSource(chatbotId, form);
    }

    setIsProcessing(false);
  }


  async function createChatSource(chatbotId, form) {
    // sending data to server
    const res = await createChatSourceByFile(chatbotId, form);
    if (res.data) {

      if (isEditMode == false) {
        toast.success("Chatbot created successfully");
      } else {
        toast.success("Chatbot retrained successfully");
      }

      history.goBack()
    } else {
      toast.error(res.message);
    }
  }

  function onFileSelect(file) {
    if (!file) return;

    setFiles((old) => {
      const arr = old.slice();
      arr.push(file);
      return arr;
    });
  }

  function onFileRemove(fileIndex) {
    setFiles((old) => {
      const arr = old.slice().filter((_, index) => index != fileIndex);
      return arr;
    });
  }

  useEffect(() => {
    if (isClear == false) return;
    setFiles([]);

    setIsClear(false);
  }, [isClear]);

  return (
    <>
      <div className="files-holder">
        <div>
          {files.length == 0 ? (
            <FileUpload onFileSelect={onFileSelect} />
          ) : (
            <div className="inner-holder">
              {files.map((value, index) => (
                <div key={index} className="file-tab">
                  <img src={fileIcon} />
                  <hr />
                  <span>{value.name}</span>
                  <button className="hover" onClick={() => onFileRemove(index)}>
                    Delete
                  </button>
                </div>
              ))}

              <button className="hover">
                Add New
                <input
                  onChange={(e) => onFileSelect(e.target.files[0])}
                  type="file"
                  accept=".pdf, .doc, .docx, .txt"
                />
              </button>
            </div>
          )}

          <hr />
          <div className="limit-container">
            <p>
              Total detected characters : <span>0</span>/400,000 limit
            </p>
            <button onClick={onSubmit} className="hover">
              {isProcessing == true ? (
                <span className="loading"></span>
              ) : (
                <>{isEditMode ? "Retrain Chatbot" : "Create Chat"}</>
              )}
            </button>
          </div>
        </div>
      </div>
    </>
  );
}
