import { Container, makeStyles } from '@material-ui/core'
import React from 'react'
import FacebookIntegrates from '../components/FacebookIntegrates'

const useStyles = makeStyles((theme) => ({
    FacebookToolMain: {
        padding: '60px',
        background: theme.palette.common.black,
        textAlign: 'center',
        [theme.breakpoints.down('sm')]: {
            padding: '15px',
        }
    },
    FacebookToolIntro: {
        padding: '0 10%',
        [theme.breakpoints.down('sm')]: {
            padding: '0',
        }
    },
    FacebookToolIntroHead: {
        color: '#FFFFFF',
        fontSize: '60px',
        fontWeight: 500,
        lineHeight: '94px',
        margin: '0',
        padding: '0 15%',
        [theme.breakpoints.down('sm')]: {
            fontSize: '24px',
            fontWeight: 600,
            lineHeight: '36px',
            padding: '0',
        }
    },
    FacebookToolIntroSubHead: {
        color: '#717280',
        fontSize: '22px',
        fontWeight: 500,
        lineHeight: '38px',
        padding: '0 3%',
        [theme.breakpoints.down('sm')]: {
            fontSize: '12px',
            fontWeight: 400,
            lineHeight: '16px',
            padding: '0'
        }
    }
}))

function FacebookTools() {
    const classes = useStyles()
    return (
        <Container maxWidth={false} className={classes.FacebookToolMain}>
            <div className={classes.FacebookToolIntro}>
                <p className={classes.FacebookToolIntroHead}>
                    Do more with UrbanChat
                    integrations
                </p>
                <p className={classes.FacebookToolIntroSubHead}>
                Urbanchat requires certain permissions to build automations with FacebookMessenger 
Click the button to grant them.
                </p>
            </div>
            <FacebookIntegrates />
        </Container>
    )
}

export default FacebookTools