import React from 'react'
import { Button, Grid, InputLabel, MenuItem, Select, makeStyles, useMediaQuery, useTheme } from '@material-ui/core'
import wa_logo from '../assets/wa_logo.png';
import * as fields from '../Utils/AllFields'
import CommonTextField from '../Utils/CommonTextField';
import { useState } from 'react';

const useStyles = makeStyles((theme) => ({
    WhatsappIntegratesMain: {
        margin: '60px 8.15% 0 8.15%',
        background: "#1F202F",
        textAlign: 'center',
        [theme.breakpoints.down('sm')]: {
            margin: '30px 4.15% 0 4.15%',
        }
    },
    WhatsappIntegratesHead: {
        background: '#38B647',
        textAlign: 'center',
        padding: '35px',
        [theme.breakpoints.down('sm')]: {
            padding: '15px',
        }
    },
    WhatsappIntegratesForm: {
        padding: '60px',
        textAlign: 'left',
        [theme.breakpoints.down('sm')]: {
            padding: '20px',
        }
    },
    WhatsappIntegratesFormTwo: {
        padding: '0 60px 0 60px',
        textAlign: 'left',
        [theme.breakpoints.down('sm')]: {
            padding: '0 20px 0 20px',
        }
    },
    WhatsappEndMenu: {
        color: '#8D99AE',
        fontSize: '20px',
        fontWeight: 600,
        lineHeight: '24px',
        [theme.breakpoints.down('sm')]: {
            fontSize: '7.14px',
            fontWeight: 600,
            lineHeight: '8.57px',
        }
    },
    WhatsappIntegratesBtn: {
        background: "#555768", padding: '22px',
        color: '#F4F5F7',
        width:'95%',
        fontSize: '24px',
        lineHeight: '24px',
        fontWeight: 600,
        textTransform: 'none',
        [theme.breakpoints.down('sm')]: {
            fontSize: '18px',
            fontWeight: 500,
            lineHeight: '20px',
        }
    },
    inviteFormTextField: {
        marginBottom: '20px',
        '& .MuiOutlinedInput-root': {
            borderRadius: '10px',
            width: '90%',
            [theme.breakpoints.down('sm')]: {
                borderRadius: '20px',
                width: '100%',
                fontSize: '16px',
                lineHeight: '18px',
                fontWeight: 500,
            },
            '& fieldset': {
                borderWidth: '2px',
                borderColor: '#808B9E',
            },
            '&:hover fieldset': {
                borderColor: '#808B9E',
            },
            '&.Mui-focused fieldset': {
                borderColor: '#808B9E',
            },
        },
    },
    inviteFormTextFieldSelect:{
        marginBottom:'30px',
        width:'95%',
        border:'1px solid #555768',
        color:'#FFFFFF'
    },
    WhatsappLogoSize: {
        [theme.breakpoints.down('sm')]: {
            width: "84px",
            height: "24px"
        },
    },
    apiProviders: {
        fontWeight: 500,
        fontSize: '34px',
        lineHeight: '36px',
        margin: 0,
        paddingBottom: '30px',
        color: '#FFFFFF'
    },
    inviteFormTextFieldlabel: {
        marginBottom: '15px',
        fontSize: '20px',
        lineHeight: '20px',
        fontWeight: 400,
        color: '#8D99AE',
        [theme.breakpoints.down('sm')]: {
            fontSize: '16px',
            lineHeight: '16px',
            fontWeight: 500,
        },
    },
    selectedBox:{
        marginBottom:'30px',
        padding:'20px',
        background:'#181924',
        color:'#4E5460',
        maxWidth:'95%',
        borderRadius:'20px',
        display:'flex',
        flexDirection:'row',
        gap:'20%',
        [theme.breakpoints.down('sm')]:{
            flexDirection:'column'
        }
    }
}))
function WhatsappIntegrates() {
    const classes = useStyles()
    const theme = useTheme()
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'))
    const whatsAppFields = fields.whatsAppInputfields
    const whatsAppFieldsTwo = fields.whatsAppInputFieldsTwo
    const [value,setValue] = useState('10')

    const handleSelectChange = (event) =>{
        setValue(event.target.value)
    }

    return (
        <div className={classes.WhatsappIntegratesMain}>
            <div className={classes.WhatsappIntegratesHead}>
                <img src={wa_logo} className={classes.WhatsappLogoSize} />
            </div>
            <Grid container className={classes.WhatsappIntegratesForm}>
                {
                    whatsAppFields.map((fields) => (
                        <Grid item xs={12} md={fields.mediumScreenSize}>
                            <CommonTextField
                                id={fields.id}
                                label={fields.label}
                                placeholder={fields.placeholder}
                                type={fields.type}
                                classes={fields.mediumScreenSize === "6" ? classes : ''}
                            />
                        </Grid>
                    ))
                }
            </Grid>
            <Grid container className={classes.WhatsappIntegratesFormTwo}>
                <Grid item xs={12}>
                    <p className={classes.apiProviders}>API Providers</p>
                </Grid>
                <Grid item xs={12}>
                    <InputLabel htmlFor={'apiProvider'} className={classes.inviteFormTextFieldlabel}>
                        Select API Provider
                    </InputLabel>
                    <Select
                        fullWidth
                        variant='outlined'
                        className={classes.inviteFormTextFieldSelect}
                        value={value}
                        onChange={handleSelectChange}
                    >
                        <MenuItem value={10}>WhatsApp Cloud API</MenuItem>
                        <MenuItem value={20}>Netcore API</MenuItem>
                        <MenuItem value={30}>None Of The Above</MenuItem>
                    </Select>
                </Grid>
                <Grid item xs={12} className={classes.selectedBox}>
                    <span>WhatsApp Cloud API</span>
                    <span>Netcore API</span>
                    <span>None Of The Above</span>
                </Grid>
                {
                    whatsAppFieldsTwo.map((fields) => (
                        <Grid item xs={12} md={fields.mediumScreenSize}>
                            <CommonTextField
                                id={fields.id}
                                label={fields.label}
                                placeholder={fields.placeholder}
                                type={fields.type}
                                classes={fields.mediumScreenSize === "6" ? classes : ''}
                            />
                        </Grid>
                    ))
                }
            </Grid>
            <div className={classes.WhatsappIntegratesForm}>
                <Grid container spacing={4}>
                    <Grid item xs={12}>
                        <Button fullWidth className={classes.WhatsappIntegratesBtn}>Get Into WhatsApp</Button>
                    </Grid>
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        <span className={classes.WhatsappEndMenu}>Terms And Conditions </span>
                    </Grid>
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        <span className={classes.WhatsappEndMenu}>Cache and Cookie </span>
                    </Grid>
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        <span className={classes.WhatsappEndMenu}>Privacy policy </span>
                    </Grid>
                </Grid>
            </div>
        </div>
    )
}

export default WhatsappIntegrates