import React, { useEffect, useRef, useState } from "react";
import "../../styles/CreateChatbot/common.css";
import "../../styles/CreateChatbot/extra-tab-common.css";
import logo from "../../assets/logo.png";
import copyIcon from "../../assets/Icons/Copy.png";
import { useParams } from "react-router-dom";
import toast from "react-hot-toast";

export default function EmbedCode() {

  const { id } = useParams()
  const [code, setCode] = useState("")
  useEffect(() => {
    setCode(`
    <script>
      const urbanChatbotId = "${id}"
    </script>
    <script src="https://urbanchat.ai/integration/chatbot-integration.js"></script>
    `)
  }, [])


  function copyCode() {

    navigator.clipboard.writeText(code)
    toast.success('Code copied')
  }

  return (
    <div className="main-content-holder embed-code">
      <div className="header">
        <img className="icon" src={logo} alt="logo" />
      </div>

      <div className="content">
        <p style={{textAlign: 'center', color: 'white'}}>To add a chat bubble to the bottom right of your website add this script tag to your html</p>
        <div className="code-area">
          <div>{code}</div>
          <button onClick={copyCode} className="hover responsive-hide">
            <div>
              <img src={copyIcon} />
            </div>
            Copy Code
          </button>
        </div>

        <button onClick={copyCode} className="hover btn-outside">
          <div>
            <img src={copyIcon} />
          </div>
          Copy Code
        </button>
      </div>
    </div>
  );
}
