import React from 'react';
import { InputLabel, TextField, useMediaQuery,useTheme} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  inviteFormTextFieldlabel: {
    marginBottom: '15px',
    fontSize: '20px',
    lineHeight: '20px',
    fontWeight: 400,
    color: '#8D99AE',
    [theme.breakpoints.down('sm')]:{
        fontSize: '16px',
    lineHeight: '16px',
    fontWeight: 500,
    },
  },
  inviteFormTextField: {
    width: '95%',
    [theme.breakpoints.down('sm')]:{
        width: '100%',
        fontSize: '16px',
        lineHeight: '18px',
        fontWeight: 500,
    },
    marginBottom: '20px',
    '& .MuiOutlinedInput-root': {
      borderRadius: '10px',
      '& fieldset': {
        borderWidth: '2px',
        borderColor: '#808B9E',
      },
      '&:hover fieldset': {
        borderColor: '#808B9E',
      },
      '&.Mui-focused fieldset': {
        borderColor: '#808B9E',
      },
    },
  },
}));

const CommonTextField = ({ id, label, placeholder, type, classes}) => {
  const commonClasses = useStyles();
  const theme = useTheme()
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'))
  return (
    <>
      <InputLabel htmlFor={id} className={classes?.inviteFormTextFieldlabel?classes?.inviteFormTextFieldlabel:commonClasses.inviteFormTextFieldlabel}>
        {label}
      </InputLabel>
      <TextField
        id={id}
        placeholder={placeholder}
        variant="outlined"
        type={type}
        fullWidth
        className={classes?.inviteFormTextField ?classes?.inviteFormTextField:commonClasses.inviteFormTextField}
        inputProps={{
          style: {
            padding: !isMobile?'20px 40px':'20px',
            color: '#808B9E',
            fontSize:!isMobile?'20px':'16px',
            fontWeight:500,
            lineHeight:!isMobile?'26px':'16px',
          },
          ...(id === 'mobile' && {
            pattern: "[0-9]*",
            inputMode: "numeric",
          })
        }}
      />
    </>
  );
};

export default CommonTextField