import React from 'react';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import FAQ from '../components/HomePage/FAQ';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import { FormControlLabel } from '@material-ui/core';
import Switch from '@material-ui/core/Switch';
import SubPlans from './SubPlans';
import { useHistory } from 'react-router-dom/cjs/react-router-dom';

const useStyles = makeStyles((theme) => ({
    '@global': {
        ul: {
            margin: 0,
            padding: 0,
            listStyle: 'none',
        },
    },
    heroContent: {
        padding: theme.spacing(5),
    },
    cardHeader: {
        backgroundColor:"#EAEAEC",
        borderRadius:'10px',
        padding:'8px',
        color:'#022B3B',
        fontSize:'24px !important',
        fontWeight:"600 !important",
        lineHeight:'24px !important'
    },
    customCardHeader:{
        fontSize:'24px',
        fontWeight:"600",
        lineHeight:'24px'
    },
    cardPricing: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'baseline',
        marginBottom: theme.spacing(2),
    },
    cardPricingMonth:{
        fontSize:'25px',
        lineHeight:'20px',
        fontWeight:700
    },
    pricingMain: {
        background: theme.palette.common.black
    },
    pricingMainText: {
        fontSize: '60px',
        fontWeight: 600,
        lineHeight: '64px',
        color: '#FFFFFF',
        fontFamily: 'Raleway'
    },
    pricingSubText: {
        fontSize: '22px',
        fontWeight: 500,
        lineHeight: '38px',
        color: '#717280',
        fontFamily: 'Raleway'
    },
    pricingFAQ: {
        padding: '100px 8.15%'
    },
    priceBTN:{
        borderRadius:'5px',
        fontSize:'24px',
        lineHeight:'24px',
        fontWeight:500,
        textTransform:'none',
        padding:'30px'
    },
    pricingList: {
        listStyle: 'none',
        paddingTop: '20px',
        paddingBottom:'100px'
    },
    pricingListItem:{
        marginTop:'17px',
        color:'#DCDFE6',
        fontSize:'17px',
        lineHeight:'20px',
        fontWeight:500
    },
    pricingSubTextPackage:{
        color:'#FFFFFF',display:'flex',flexDirection:'row',gap:'20px',justifyContent:'center',alignItems:'center',marginTop:'30px',fontSize:'28px',lineHeight:'38px',fontWeight:500
    }
}));

const IOSSwitch = withStyles((theme) => ({
    root: {
      width: 42,
      height: 26,
      padding: 0,
      margin: theme.spacing(1),
    },
    switchBase: {
      padding: 1,
      '&$checked': {
        transform: 'translateX(16px)',
        color: theme.palette.common.white,
        '& + $track': {
          backgroundColor: '#52d869',
          opacity: 1,
          border: 'none',
        },
      },
      '&$focusVisible $thumb': {
        color: '#52d869',
        border: '6px solid #fff',
      },
    },
    thumb: {
      width: 24,
      height: 24,
    },
    track: {
      borderRadius: 26 / 2,
      border: "1px solid #2B2D42",
      backgroundColor: "#2B2D42",
      opacity: 1,
      transition: theme.transitions.create(['background-color', 'border']),
    },
    checked: {},
    focusVisible: {},
  }))(({ classes, ...props }) => {
    return (
      <Switch
        focusVisibleClassName={classes.focusVisible}
        disableRipple
        classes={{
          root: classes.root,
          switchBase: classes.switchBase,
          thumb: classes.thumb,
          track: classes.track,
          checked: classes.checked,
        }}
        {...props}
      />
    );
  });

const commonDescription = [
    "Facebook Integration",
    "Whatsapp Integration",
    "Shopify Integration",
    "Wordpress Integration",
    "Manual Code Embed",
    "Chatbot Customization",
    "Data Source",
    "PDF Upload",
    "Website Links Crawler",
    "Sitemap Links Crawler",
    "Support"
  ];

const tiers = [
    {
        price: '15.99',
        description: ["25000 characters",...commonDescription],
        buttonText: 'Subscribe',
        buttonVariant: 'contained',
    },
    {
        title: 'Best Value',
        price: '9.99',
        description: ["10000 characters",...commonDescription],          
        buttonText: 'Subscribe',
        buttonVariant: 'contained',
    },
    {
        price: '55.99',
        description: ["55000 characters",...commonDescription], 
        buttonText: 'Subscribe',
        buttonVariant: 'contained',
    },
];



export default function Pricing() {
    const classes = useStyles();
    const history = useHistory()
    const [state, setState] = React.useState({
        checkedB: false,
      });
    
      const handleChange = (event) => {
        setState({ ...state, [event.target.name]: event.target.checked });
      };

    const handleSubscribe = () =>{
        history.push('/pricing/payment')
    }

    return (
        <div className={classes.pricingMain}>
            <Container maxWidth="sm" component="main" className={classes.heroContent}>
                <Typography component="h1" variant="h2" align="center" className={classes.pricingMainText} gutterBottom>
                    Pricing Plans
                </Typography>
                <Typography variant="h5" align="center" className={classes.pricingSubText} component="p">
                    Get additional 45 Days by subscribing yearly...!
                </Typography>
                <div className={classes.pricingSubTextPackage}>Monthly  <FormControlLabel
                    control={
                        <IOSSwitch
                            checked={state.checkedB}
                            onChange={handleChange}
                            name="checkedB"
                        />
                    }
                />   Yearly <br/>save 25%</div>
            </Container>
            <Container maxWidth="lg" component="main">
                <Grid container spacing={10} alignItems="flex-end">
                    {tiers.map((tier) => (
                        <Grid item key={tier.title} xs={12} sm={tier.title === 'Enterprise' ? 12 : 6} md={4}>
                            <Card style={{background:!tier.title?'#27293C':'#022B3B'}}>
                                {
                                    !tier.title ? "" :
                                        <CardHeader
                                            title={tier?.title}
                                            subheader={tier?.subheader}
                                            subheaderTypographyProps={{ align: 'center' }}
                                            className={classes.cardHeader}
                                            titleTypographyProps={{
                                                align: 'center',
                                                className: classes.customCardHeader,
                                            }}
                                        />
                                }
                                <CardContent style={{paddingTop:'50px',color:'#FFFFFF'}}>
                                    <div className={classes.cardPricing}>
                                        <Typography component="h2" variant="h3">
                                            €{tier.price}
                                        </Typography>
                                    </div>
                                    <div className={classes.cardPricing}>
                                        <Typography variant="h6" className={classes.cardPricingMonth} style={{color:'#808B9E'}}>
                                            /month
                                        </Typography>
                                    </div>
                                    <ul className={classes.pricingList}>
                                        {tier.description.map((line) => (
                                            <Typography component="li" variant="subtitle1" align="left" key={line} className={classes.pricingListItem}>
                                               <FiberManualRecordIcon style={{color:'rgba(56, 182, 71, 0.5)',width:'10px',height:'10px',marginRight:'20px'}}/> {line}
                                            </Typography>
                                        ))}
                                    </ul>
                                </CardContent>
                                <Button fullWidth variant={tier.buttonVariant} style={{background:!tier.title?'#555768':'',color:!tier.title?'#FFFFFF':'#022B3B'}} className={classes.priceBTN} 
                                // onClick={handleSubscribe}
                                >
                                        {tier.buttonText}
                                </Button>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            </Container>
            <div className={classes.pricingFAQ}>
                <SubPlans/>
            </div>
            <div className={classes.pricingFAQ}>
                <FAQ />
            </div>
        </div>
    );
}