import { Button, Container, Grid, InputLabel, TextField, makeStyles, useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import main_logo from "../assets/main_logo.png";
import formMobile from "../assets/formMobile.png"
import * as Fields from "../Utils/AllFields"
import CommonTextField from '../Utils/CommonTextField';

const useStyles = makeStyles((theme) => ({
    inviteFormContainer: {
        background: '#1F202F'
    },
    inviteFormHead: {
        background: "#2B2D42",
        padding: '15px 60px'
    },
    inviteFormLogo: {
        width: '152px',
        height: '42px',
        [theme.breakpoints.down('sm')]: {
            width: '101px',
            height: '28px',
        }
    },
    inviteForm: {
        padding: '30px 60px',
        [theme.breakpoints.down('sm')]: {
            padding: '20px',
        }
    },
    inviteFormHeading: {
        color: '#FFFFFF',
        fontSize: '34px',
        lineHeight: '36px',
        fontWeight: 500,
        [theme.breakpoints.down('sm')]: {
            fontSize: '20px',
            lineHeight: '18px',
        }
    },
    inviteFormTextFieldlabel: {
        marginBottom: '15px',
        fontSize: '20px',
        lineHeight: '20px',
        fontWeight: 400,
        color: '#8D99AE',
        [theme.breakpoints.down('sm')]: {
            fontSize: '16px',
            lineHeight: '16px',
            fontWeight: 500,
        },
    },
    inviteFormTextField: {
        width: '80%',
        [theme.breakpoints.down('sm')]: {
            width: '100%',
            fontSize: '16px',
            lineHeight: '18px',
            fontWeight: 500,
        },
        marginBottom: '20px',
        '& .MuiOutlinedInput-root': {
            borderRadius: '10px',
            '& fieldset': {
                borderWidth: '2px',
                borderColor: '#808B9E',
            },
            '&:hover fieldset': {
                borderColor: '#808B9E',
            },
            '&.Mui-focused fieldset': {
                borderColor: '#808B9E',
            },
        },
    },
    inviteFormContainerBtn: {
        background: '#181924',
        textAlign: 'center',
        width: '80%',
        padding: '25px',
        fontSize: '24px',
        lineHeight: '24px',
        fontWeight: 500,
        color: '#FFFFFF',
        borderRadius: '15px',
        [theme.breakpoints.down('sm')]: {
            fontSize: '16px',
            lineHeight: '16px',
            fontWeight: 500,
            width: '100%',
        }
    }
}))

function Inviteform() {
    const classes = useStyles()
    const theme = useTheme()
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'))

    const formInputfields = Fields.formInputfields;
    return (
        <Container>
            <Grid container className={classes.inviteFormContainer}>
                <Grid item xs={12} className={classes.inviteFormHead}>
                    <img src={main_logo} className={classes.inviteFormLogo} />
                </Grid>
                <Grid container className={classes.inviteForm}>
                    <Grid item xs={12} className={classes.inviteFormHeading} style={{ paddingBottom: '30px' }}>
                        <span>Your Friend Details</span>
                    </Grid>
                    <Grid item xs={isMobile ? 12 : 7}>
                        {formInputfields.map((field) => (
                            <Grid item xs={12} key={field.id}>
                                <CommonTextField
                                    id={field.id}
                                    label={field.label}
                                    placeholder={field.placeholder}
                                    type={field.type}
                                    classes={classes}
                                />
                            </Grid>
                        ))}
                        <Grid item xs={12}>
                            <InputLabel htmlFor='About Urbanchat.Ai' className={classes.inviteFormTextFieldlabel}>About Urbanchat.Ai</InputLabel>
                            <TextField
                                className={classes.inviteFormTextField}
                                id="about-urbanchat"
                                multiline
                                minRows={5}
                                maxRows={5}
                                variant="outlined"
                                placeholder='For gamified applications or platforms seeking higher user engagement, the AI can ask questions to keep users thinking and engaged. E.g., "Have you ever thought about why the sky is blue?" '
                                fullWidth
                                inputProps={{
                                    style: {
                                        padding: '20px',
                                        color: '#808B9E',
                                        fontSize: '20px',
                                        fontWeight: 500,
                                        lineHeight: '36px'
                                    }
                                }}
                            />
                        </Grid>
                    </Grid>
                    {!isMobile &&
                        (<Grid item xs={5}>
                            <img src={formMobile} style={{ margin: '60px 30px 0 0' }} />
                        </Grid>)}
                    <Grid item xs={isMobile ? 12 : 7}>
                        <Button className={classes.inviteFormContainerBtn} fullWidth>Invite Now</Button>
                    </Grid>
                </Grid>
            </Grid>
        </Container>
    )
}

export default Inviteform