import React, { useContext, useEffect, useState } from "react";
import { updateChatbotById } from "../../../lib/api/ApiUpdateChatbot";
import toast from "react-hot-toast";
import { ServerDataContext } from "../../../pages/MyChatbot/UpdateChatbot";
import { useParams } from "react-router-dom";


export default function Domain() {

  const { serverData, setServerData } = useContext(ServerDataContext)
  const [isSaving, setIsSaving] = useState(false)
  const [domains, setDomains] = useState(["abhishek"])

  const { id } = useParams();



  //domains


  async function onSave() {

    const data = [...domains].filter((item) => item != "").join(',')
    console.log(data)

    if (isSaving) return

    setIsSaving(true)
    const res = await updateChatbotById(id, {allowed_domains: data})
    if (res.data) {
      toast.success('Updated successfully')
      setServerData((old) => {
        const raw = structuredClone(old)
        raw.view[0].allowed_domains = data
        return raw
      })


    } else {
      toast.error(res.message)
    }

    setIsSaving(false)

    setDomains((old) => [...old].filter((item) => item != ""))
  }



  useEffect(() => {

    // set state
    // formData.current.default_message = serverData.view[0].default_message
    setDomains(serverData.view[0].allowed_domains.split(','))

  }, []);



  return (
    <div>
      <div>
        <h2>Domain</h2>

        <h3>Link</h3>

        {domains.map((domain, index) => (
          <div key={index} className="link-container">
            <input
              type="text"
              value={domain}
              onChange={(e) => {
                const updatedDomains = [...domains];
                updatedDomains[index] = e.target.value;
                setDomains(updatedDomains);
              }}
            />
            <button className="hover">Verify Link</button>
          </div>
        ))}

        <div className="clean-all">
          <p className="note">Enter each message in a new line.</p>
          <button className="hover primary" onClick={() => setDomains((old) => [...old, ""])}>Add Link</button>
        </div>


        <div className="button-holder">
          <button className="secondary">Previous</button>
          <button onClick={onSave} className="primary">{isSaving ? (<span className="loading mini"></span>) : "Save & Next"}</button>
        </div>
      </div>
    </div>
  );
}
