import { makeStyles } from '@material-ui/core'
import React from 'react'
import friends_line from '../assets/friends_line.png'
import Inviteform from '../components/Inviteform'

const useStyles = makeStyles((theme) => ({
    inviteMain: {
        background: theme.palette.common.black,
        paddingTop: '30px',
        paddingBottom: '100px',
    },
    inviteHeadText: {
        fontSize: '40px',
        fontWeight: 500,
        lineHeight: '34px',
        color: '#FFFFFF',
        margin: '0 auto',
        width: '84%',
        [theme.breakpoints.down('sm')]: {
            width: '90%',
            fontSize: '24px',
        fontWeight: 600,
        lineHeight: '26px',
        }
    },
    inviteText: {
        fontSize: '22px',
        fontWeight: 500,
        lineHeight: '38px',
        color: '#717280',
        width: '84%',
        margin: '30px auto',
        [theme.breakpoints.down('sm')]: {
            width: '90%',
            fontSize: '12px',
        fontWeight: 400,
        lineHeight: '16px',
        }
    },
    inviteLoop: {
        marginLeft: '-100px',
        width:'180px',
        [theme.breakpoints.down('sm')]: {
            width: '91px',
            marginTop:'10px'
        }
    }
}))
function Invite() {
    const classes = useStyles()
    return (
        <div className={classes.inviteMain}>
            <h5 className={classes.inviteHeadText}>Invite A Friends<img src={friends_line} className={classes.inviteLoop} alt='loop' /></h5>
            <p className={classes.inviteText}>
                Join Friends of UrbanChat.co and receive a 20% commission on all payments within the first 12 months for paying customers you refer to
            </p>
            <Inviteform />
        </div>
    )
}

export default Invite