import React from "react";
import "../../styles/CreateChatbot/common.css";
import "../../styles/CreateChatbot/extra-tab-common.css";
import logo from "../../assets/logo.png";
import copyIcon from "../../assets/Icons/CopyIcon.svg";

export default function Share() {
  return (
    <div className="main-content-holder share-tab">
      <div className="header">
        <img className="icon" src={logo} alt="logo" />
      </div>

      <div className="content">
        <div className="link-container">
          <input disabled type="text" value={'www.apple.com'}/>
          <button className="hover"><div className="icon"><img src={copyIcon}/></div> Copy Link</button>
        </div>
      </div>
    </div>
  );
}
