import React from 'react'
import { Button, Grid,makeStyles, useMediaQuery, useTheme } from '@material-ui/core'
import FB_messenger from '../assets/FB_messenger.png';
import * as Fields from "../Utils/AllFields"
import CommonTextField from '../Utils/CommonTextField';

const useStyles = makeStyles((theme) => ({
    facebookIntegratesMain: {
        margin: '60px 8.15% 0 8.15%',
        background: "#1F202F",
        textAlign: 'center',
        [theme.breakpoints.down('sm')]: {
            margin: '30px 4.15% 0 4.15%',
        }
    },
    facebookIntegratesHead: {
        background: '#0063E2',
        textAlign: 'center',
        padding: '35px',
        [theme.breakpoints.down('sm')]: {
            padding: '15px',
        }
    },
    facebookIntegratesForm: {
        padding: '60px',
        textAlign: 'left',
        [theme.breakpoints.down('sm')]:{
            padding: '20px',
        }
    },
    facebookformHead: {
        color: '#FFFFFF',
        fontSize: '34px',
        fontWeight: 600,
        lineHeight: '36px',
        [theme.breakpoints.down('sm')]:{
            fontSize: '18px',
            fontWeight: 500,
            lineHeight: '20px',  
        }
    },
    facebookEndMenu: {
        color: '#8D99AE',
        fontSize: '20px',
        fontWeight: 600,
        lineHeight: '24px',
        [theme.breakpoints.down('sm')]:{
            fontSize: '7.14px',
            fontWeight: 600,
            lineHeight: '8.57px',  
        }
    },
    facebookIntegratesBtn: {
        background: "#555768", padding: '22px',
        color: '#F4F5F7',
        fontSize: '24px',
        lineHeight: '24px',
        fontWeight: 600,
        textTransform: 'none',
        marginBottom:'20px',
        [theme.breakpoints.down('sm')]:{
            fontSize: '18px',
            fontWeight: 500,
            lineHeight: '20px',  
        }
    },
    facebookNoteSection:{
        display: 'flex', alignItems: 'center'
      },
      facebookNoteIcon:{
        color: '#FFFFFF', background: '#8D99AE', width: '62px', height: '44px', borderRadius: '30px', display: 'flex', justifyContent: 'center', alignItems: 'center' ,
        [theme.breakpoints.down('sm')]:{
            width: '45px', height: '24px', borderRadius: '20px',fontSize:'9px'
        },
      },
    facebookNoteText: {
        marginLeft: '20px', color: '#646D7C',
        fontSize: '18px',
        fontWeight: 400,
        lineHeight: '20px',
        [theme.breakpoints.down('sm')]:{
            fontSize: '10px',
            lineHeight: '16px',
            fontWeight: 500,
        },
    },
    inviteFormTextField: {
        width: '100%',
        [theme.breakpoints.down('sm')]:{
            width: '100%',
            fontSize: '16px',
            lineHeight: '18px',
            fontWeight: 500,
        },
        marginBottom: '20px',
        '& .MuiOutlinedInput-root': {
          borderRadius: '10px',
          '& fieldset': {
            borderWidth: '2px',
            borderColor: '#808B9E',
          },
          '&:hover fieldset': {
            borderColor: '#808B9E',
          },
          '&.Mui-focused fieldset': {
            borderColor: '#808B9E',
          },
        },
      },
      fbLogoSize:{
        [theme.breakpoints.down('sm')]:{
            width:"154px",
            height:"24px"
        },
      }
}))
function FacebookIntegrates() {
    const classes = useStyles()
    const formInputfields = Fields.fBInputfields;
    const theme = useTheme()
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'))
    return (
        <div className={classes.facebookIntegratesMain}>
            <div className={classes.facebookIntegratesHead}>
                <img src={FB_messenger} className={classes.fbLogoSize}/>
            </div>
            <div className={classes.facebookIntegratesForm}>
                <Grid container spacing={4}>
                    <Grid item xs={12}>
                        <span className={classes.facebookformHead}>Log in</span>
                    </Grid>
                    {formInputfields.map((field) => (
                            <Grid item md={6} xs={12} key={field.id}>
                                <CommonTextField
                                    id={field.id}
                                    label={field.label}
                                    placeholder={field.placeholder}
                                    type={field.type}
                                    classes={classes}
                                />
                            </Grid>
                        ))}
                    <Grid className={classes.facebookNoteSection} item xs={12}>
                        <span className={classes.facebookNoteIcon}>Note</span>
                        <span className={classes.facebookNoteText}>This will crawl all the links starting with the URL (not including files on the website).</span>
                    </Grid>
                    <Grid item xs={12}>
                        <Button fullWidth className={classes.facebookIntegratesBtn}>
                            {isMobile?"Login":"Connect into Facebook Messenger"}
                        </Button>
                    </Grid>
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        <span className={classes.facebookEndMenu}>Terms And Conditions </span>
                    </Grid>
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        <span className={classes.facebookEndMenu}>Cache and Cookie </span>
                    </Grid>
                    <Grid item xs={4} style={{ textAlign: 'center' }}>
                        <span className={classes.facebookEndMenu}>Privacy policy </span>
                    </Grid>
                </Grid>
            </div>
        </div>
    )
}

export default FacebookIntegrates