import { urbanApi } from "./api";
import HostName from "../Utils/HostName";

class UrbanApiService {
    constructor(){
        this.hostName = HostName()
    }
    loginCall = (data)=>{
        return urbanApi.post(`api/login`,data);
    }
    signUpCall = (data)=>{
        return urbanApi.post(`api/register-user`,data)
    }
}
export default new UrbanApiService()