import { Typography, makeStyles } from '@material-ui/core'
import React from 'react'

const useStyles = makeStyles((theme) => ({
    privacyMain: {
        background: '#0F0F16', color: '#FFFFFF', padding: '5%', fontSize: "16px", lineHeight: '28px', fontWeight: 400
    },
    privacyHead: {
        fontWeight: 900,
        fontSize: '32px',
        lineHeight: '40px'
    },
    privacyHeading:{
        fontWeight:700
    }
}))

function Privacy() {
    const classes = useStyles()
    return (
        <div className={classes.privacyMain}>
            <Typography align='left' variant='body'>
                <p className={classes.privacyHead}>Privacy Policy</p>

                <p>Last updated: 10th November, 2023</p>

                <p className={classes.privacyHeading}>Introduction</p>

                <p>Kindly review this Privacy Policy ("Privacy Policy") prior to utilizing our Service, inclusive of the Website, Widget, and API (as outlined below), collectively referred to as the "Service". This Privacy Policy outlines the variety of information and data we gather and our methods of utilization and dissemination of this information. Your engagement with and usage of the Service is conditional upon your acceptance of the Terms of Service available at the following link: https://www.urbanchat.ai/terms ("Terms of Service"), which encompass the terms of the Privacy Policy detailed herein. Urbanchat.ai ("Company") manages the Service. We leverage your data to enhance and deliver the Service. By engaging with the Service, you consent to the gathering and utilization of information in line with this policy. Unless explicitly defined in this Privacy Policy, the terms herein align with those in our Terms of Service.</p>

                <p className={classes.privacyHeading}>Definitions</p>

                <p>API refers to the Urbanchat application programming interface designed for integration with the User's software.
                Cookies refer to small files stored on your Device.
                Device signifies a computer or mobile device.</p>

                <p>Data Controller denotes a natural or legal entity that autonomously or collaboratively determines the objectives and means of processing personal data. In the context of this Privacy Policy, we act as the Data Controller of your data.
                Data Processors (or Service Providers) are entities that manage data on behalf of the Data Controller. We may employ various Service Providers to efficiently process your data.</p>

                <p>Data Subject refers to any living individual who is the focus of Personal Data.
                Personal Data pertains to information about a living individual who can be identified from that data (or from that and other information in our possession or likely to be obtained).</p>

                <p>Service encompasses the Website, Widget, and/or the API (as applicable to a specific User).
                Usage Data is information collected automatically from the use of the Service or from the Service infrastructure itself (for instance, the duration of a page visit).</p>

                <p>User denotes an individual utilizing our Service. The User aligns with the Data Subject, who is the focus of Personal Data.
                Website refers to web pages located at urbanchat.ai.</p>

                <p>Widget signifies an Urbanchat widget that can be integrated into the User's website.</p>

                <p className={classes.privacyHeading}>The Data Controller</p>

                <p>The entity controlling your Personal Data is: Urbanchat.ai.
                Information Collection and Use
                We gather diverse types of information for several reasons to enhance and deliver our Service to you.
                Types of Data Collected
                Personal Data</p>

                <p>During your interaction with our Service, we may request you to supply certain personally identifiable information that can be employed to get in touch with or identify you ("Personal Data"), including:
                Email address,
                First name and last name,
                Cookies and Usage Data.
                Usage Data
                We might also gather details that your browser transmits whenever you access our Service, or when you utilize the Service via a Device ("Usage Data"). This Usage Data may encompass details such as your computer's Internet Protocol address (e.g., IP address), browser type, browser version, pages of our Service visited, the time and date of your visit, time spent on those pages, unique Device identifiers, and other diagnostic data. When accessing the Service with a Device, the Usage Data may include details such as the type of Device you employ, your Device unique ID, the IP address of your Device, your operating system, the type of Internet browser used, unique Device identifiers, and other diagnostic data.</p>

                <p className={classes.privacyHeading}>Tracking Cookies Data</p>

                <p>We utilize cookies and analogous tracking technologies to monitor activity on our Service and retain specific information. Cookies are files containing a minimal amount of data, potentially including an anonymous unique identifier. Cookies are dispatched from a website to your browser and saved on your Device. Additional tracking technologies such as beacons, tags, and scripts are also employed to gather and track information to enhance and analyze our Service. You can direct your browser to reject all cookies or to notify when a cookie is being dispatched. Nonetheless, declining cookies may render some parts of our Service inaccessible. Examples of Cookies we employ:</p>

                <p>Session Cookies: We use Session Cookies to manage our Service.
                Preference Cookies: Preference Cookies are utilized to recall your preferences and diverse settings.
                Security Cookies: We employ Security Cookies for safeguarding purposes.
                Advertising Cookies: Advertising Cookies aim to present advertisements pertinent to you and your interests.</p>

                <p className={classes.privacyHeading}>6. Utilization of Data</p>
                <p>Urbanchat.ai leverages the amassed Personal Data for a variety of reasons:</p>

                <p>To deliver and sustain our Service; type of Personal Data: email address, first name and last name, Cookies and Usage Data; essential for executing a contract you are part of.</p>
                <p>To inform you of alterations to our Service; type of Personal Data: email address, first name and last name, Cookies and Usage Data; essential for executing a contract you are part of.</p>
                <p>To enable participation in interactive features of our Service at your discretion; type of Personal Data: email address, first name and last name, Cookies and Usage Data; essential for executing a contract you are part of.</p>
                <p>To provide customer assistance; type of Personal Data: email address, first name and last name, Cookies and Usage Data; essential for executing a contract you are part of.</p>
                <p>To gather analysis or valuable data to enhance our Service; type of Personal Data: email address, first name and last name, Cookies and Usage Data; legitimate interests of the Data Controller.</p>
                <p>To oversee the usage of our Service; type of Personal Data: email address, first name and last name, Cookies and Usage Data; legitimate interests of the Data Controller.</p>
                <p>To identify, avert, and tackle technical issues; type of Personal Data: email address, first name and last name, Cookies and Usage Data; legitimate interests of the Data Controller.</p>
                <p>To fulfill other purposes for which you provide it; type of Personal Data: email address, first name and last name, Cookies and Usage Data; essential for executing a contract you are part of.</p>
                <p>To execute our obligations and enforce our rights arising from contracts between you and us, including billing and collection; type of Personal Data: email address, first name and last name, Cookies and Usage Data; essential for executing a contract you are part of.</p>
                <p>To provide notices about your account and/or subscription, including expiration and renewal notices, email-instructions, etc.; type of Personal Data: email address, first name and last name, Cookies and Usage Data; essential for executing a contract you are part of.</p>
                <p>To provide news, special offers, and information about similar goods, services, and events unless you opt out; type of Personal Data: email address, first name and last name, Cookies and Usage Data; with your consent.</p>
                <p>In any other manner described when the information is provided; type of Personal Data: email address, first name and last name, Cookies and Usage Data; essential for executing a contract you are part of.</p>

                <p className={classes.privacyHeading}>7. Retention of Data</p>
                <p>We will preserve your Personal Data only as long as necessary for the purposes outlined in this Privacy Policy. We will store and use your Personal Data to the extent required to comply with legal obligations, resolve disputes, and enforce legal agreements and policies. Personal Data processed with your consent will be stored as long as the relevant consent is not withdrawn and until the expiration of claims arising from the Service. Usage Data will also be retained for internal analysis. Usage Data is typically retained for a shorter duration, except when used to enhance security or functionality, or when legally mandated for longer retention periods.</p>

                <p className={classes.privacyHeading}>8. Transfer of Data</p>
                <p>Your data, including Personal Data, may be transferred to – and maintained on – computers situated outside your state, province, country, or other governmental jurisdiction where data protection laws may vary. If you are situated outside the United States and choose to provide information, note that we transfer the data, including Personal Data, to the United States for processing. Urbanchat.ai will undertake all reasonably necessary measures to ensure your data is securely treated in accordance with this Privacy Policy and no transfer of your Personal Data will occur to an organization or country unless there are sufficient controls in place, including the security of your data and personal information. When transferring your Personal Data to other countries, we will protect that data as outlined in this Privacy Policy and in compliance with applicable law. We use contractual protections for the transfer of Personal Data among various jurisdictions (the European Commission’s standard contractual clauses referred to in Article 46. 2 c) of the GDPR.)</p>

                <p className={classes.privacyHeading}>9. Disclosure of Data</p>
                <p>We might share the Personal Data you provide in the following situations:

                In compliance with legal obligations or in response to requests from governmental bodies;
                In the event of a corporate restructuring, such as a merger, acquisition, or asset sale;
                With our affiliates, subsidiaries, and employees;
                With contractors and third parties that assist in our operations;
                For purposes explicitly communicated to you;
                To display your company's logo on our Website;
                In other scenarios, with your explicit consent.
                Your Personal Data is not sold or shared in ways other than what is outlined in this Privacy Policy.</p>

                <p className={classes.privacyHeading}>10. Security of Data</p>
                <p>We prioritize the protection of your data. However, it's important to recognize that no data transmission method over the Internet or electronic storage system is entirely secure. We implement suitable administrative, technical, and physical measures to safeguard the Personal Data you provide against unauthorized access, disclosure, alteration, loss, or destruction. For example, we maintain backups and ensure only authorized personnel have access to Personal Data.</p>

                <p className={classes.privacyHeading}>11. Your Data Protection Rights Under General Data Protection Regulation (GDPR)</p>
                <p>Residents of the European Union (EU) and European Economic Area (EEA) have specific rights pertaining to data protection under the GDPR. We strive to enable you to amend, correct, delete, or restrict the use of your Personal Data. To exercise any of these rights, please contact us at info@urbanchat.ai.</p>

                <p className={classes.privacyHeading}>12. Your Data Protection Rights under the California Privacy Protection Act (CalOPPA)</p>
                <p>CalOPPA mandates commercial websites and online services to display a privacy policy. It extends beyond California, requiring any entity in the United States (and possibly globally) that operates websites collecting identifiable information from California consumers to display a privacy policy on its website detailing the information being gathered and shared. Consistent with CalOPPA:</p>

                <p>Users can visit our site anonymously;
                Our Privacy Policy link contains the word “Privacy” and can be easily located on our homepage;
                Users will be informed of any changes to our privacy policy on our Privacy Policy Page;
                Users can modify their personal information by emailing us at info@urbanchat.ai.</p>

                <p className={classes.privacyHeading}>13. Service Providers</p>
                <p>We may engage third-party companies and individuals to facilitate our Service, provide the Service on our behalf, perform Service-related tasks, or assist us in understanding how our Service is utilized. These third parties can access your Personal Data only to perform these tasks for us and are obligated not to disclose or use it for other purposes.</p>

                <p className={classes.privacyHeading}>14. Analytics</p>
                <p>We may engage third-party Service Providers to observe and analyze the usage of our Service, such as Google Analytics and Mixpanel.</p>

                <p className={classes.privacyHeading}>15. Payments</p>
                <p>We might offer paid products and/or services within the Service. In such cases, we use third-party services for payment processing. Your payment details are provided directly to our third-party payment processors, and the use of your personal information is governed by their Privacy Policies.</p>

                <p className={classes.privacyHeading}>16. Links to Other Sites</p>
                <p>Our Service may contain links to sites not operated by us. If you click on a third-party link, you'll be directed to their site. We recommend reviewing the Privacy Policy of every site you visit. We don't control and aren't responsible for the content, privacy policies, or practices of third-party sites or services.</p>

                <p className={classes.privacyHeading}>17. Children's Privacy</p>
                <p>Our Service is not intended for individuals under the age of 16. We do not intentionally gather identifiable information from anyone under 16. If you are a parent or guardian and know that your child has provided us with Personal Data, please contact us. If we learn that we have collected Personal Data from children without parental consent verification, we take steps to remove that information from our servers.</p>

                <p className={classes.privacyHeading}>18. Changes to This Privacy Policy</p>
                <p>We may periodically update our Privacy Policy. We will inform you of any changes by posting the updated Privacy Policy on this page and updating the “effective date” at the top of this page. We encourage you to review this Privacy Policy periodically for any changes. By continuing to use our Service or providing us with Personal Data after we have posted an updated Privacy Policy, you consent to the revised Privacy Policy and practices described in it.</p>

                <p className={classes.privacyHeading}>19. Contact Us</p>
                If you have any questions about this Privacy Policy, please contact us at info@urbanchat.ai.
                <p className={classes.privacyHeading}>20. Privacy Policy Addendum</p>
                Addendum to Privacy Policy for Canada

                <p>Personal Data that our affiliates and third-party service providers manage and process in the U.S. and other foreign countries may be open to lawful access requests from U.S. or foreign courts or governmental authorities. We refrain from giving your information to third parties for promotional purposes without your explicit consent. For inquiries about our privacy practices, to access, modify, or correct inaccuracies in your personal information, or to raise a question or concern about how we or our service providers handle your personal information, please reach out to us using the contact details mentioned in the “Contact Us” section above.</p>

                <p className={classes.privacyHeading}>Addendum to Privacy Policy for Mexico</p>

                <p>If Mexican privacy laws or regulations are applicable, the following additional provisions specific to Mexico apply and take precedence over conflicting provisions in the Privacy Policy.</p>

                <p className={classes.privacyHeading}>How We Use the Information We Obtain</p>

                <p>We may also use your Personal Data for the purposes mentioned in the “Use of Data” section of the Privacy Policy, which may be considered secondary purposes under Mexican law. We restrict the use or sharing of personal payment or financial information to payment processing or other legally permitted activities. You can revoke your consent for our processing of your Personal Data by contacting us as mentioned in the “How to Contact Us” section of the Privacy Policy. Revoking consent for processing Personal Data for primary purposes may limit our ability to provide services, although we may still conduct certain processing permitted by law. The Company may also leverage data and related analysis in other Company products and services.</p>

                <p className={classes.privacyHeading}>Information We Share</p>

                <p>We may share your Personal Data as outlined in the Privacy Policy. Subject to legal limits, you can revoke your consent to this sharing by contacting us as indicated in the “How to Contact Us” section of the Privacy Policy. We may also share obtained information with our customers, partners, affiliates, and joint marketing partners, referred to collectively as “Business Partners,” for purposes outlined in this Privacy Policy. Information may also be shared with Business Partners and other third parties for warranty, troubleshooting, maintenance, or to enhance the design and functionality of their products and services.</p>

                <p className={classes.privacyHeading}>Requests for Access, Correction, Cancellation, Objection or Consent Withdrawal</p>

                <p>Beyond the rights granted in the Privacy Policy, you may request cancellation of your Personal Data within legal limits by contacting us as indicated in the “How to Contact Us” section of the Privacy Policy. After a valid cancellation request, we may retain the Personal Data for durations and purposes allowed by law before deletion. We will respond to requests to access, correct, cancel, object, or withdraw consent for processing Personal Data within 20 business days from receiving a complete request or as mandated by law. A complete request must include your full name, contact address, and a clear, detailed description of your request.</p>

                <p className={classes.privacyHeading}>Addendum to Privacy Policy for Japan</p>

                <p>The Company adheres to Japanese laws and regulations, including the Act on the Protection of Personal Information. The Company bears primary responsibility for managing Personal Data that is jointly used with our affiliates or third parties. We won't share your information with third parties for marketing purposes without your explicit consent.</p>

                <p className={classes.privacyHeading}>Addendum to Privacy Policy for Republic of Korea</p>
                Unless mandated by law, consumer Personal Data is securely discarded without delay when (i) the consumer withdraws consent for our use of the information, (ii) the purpose of collecting and using the Personal Data is achieved, or (iii) the legal retention period concludes. If law mandates preservation of Personal Data that would otherwise be discarded, the Personal Data is moved to a separate database and then discarded after the legally determined period. Personal Data is discarded using a method that reasonably prevents restoration or reuse. We will not share your Personal Data with third parties in a manner that violates the law (such as without required consent). For the purposes outlined in the Privacy Policy, the Company or our affiliates are accountable for managing Personal Data related to services provided in the Republic of Korea.
            </Typography>
        </div>
    )
}

export default Privacy