import * as React from 'react';
import Accordion from '@material-ui/core/Accordion';  // Import from "@material-ui/core" for the core components
import AccordionSummary from '@material-ui/core/AccordionSummary';  // Import from "@material-ui/core"
import AccordionDetails from '@material-ui/core/AccordionDetails';  // Import from "@material-ui/core"
import Typography from '@material-ui/core/Typography';  // Import from "@material-ui/core"
import { makeStyles } from '@material-ui/styles';
import Grid from '@material-ui/core/Grid';  // Import from "@material-ui/core"
import { Container } from '@material-ui/core';
import faqData from '../../faqData.json'
import RemoveIcon from '@material-ui/icons/Remove';  // Import from "@material-ui/icons"
import AddIcon from '@material-ui/icons/Add';  // Import from "@material-ui/icons"

const useStyles = makeStyles((theme) => ({
    faqHead: {
        paddingBottom: '50px',
        backgroundColor: 'var(--primarycolours-900, #0F0F16)',
        color: '#FFFFFF',
        // padding: '0 10%',
        [theme.breakpoints.down("md")]: {
            padding: '0px',
        }
    },
    faqMaintext: {
        fontSize: '50px',
        fontWeight: 500,
        lineHeight: '64px',
        letterSpacing: '0em',
        [theme.breakpoints.down("sm")]: {
            fontSize: '28px',
            lineHeight:'38px'
        }
    },
    accordion: {
        backgroundColor: 'var(--primarycolours-900, #0F0F16) !important',
        color: '#FFFFFF !important',
        borderBottom: '1px solid #1F202F',
        '&:first-child': {
            borderTop: '1px solid #1F202F !important',
        },
        fontSize: '22px',
        fontWeight: 500,
        lineHeight: '26px',
        letterSpacing: '0em',
    },
    accordionSummary: {
        minHeight: '80px !important'
    },
    accordionIcon: {
        marginLeft: 'auto',
        color: 'white',
    },
    accordionText:{
        fontSize:'22px'
    }
}));

export default function FAQ() {
    const classes = useStyles();
    const [expanded, setExpanded] = React.useState(null);

    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : null);
    };

    return (
        <Container className={classes.faqHead} maxWidth={false}>
            <Grid container>
                <Grid item xs={12}>
                    <div className={classes.faqHead}>
                        <h1 className={classes.faqMaintext}>Frequently asked questions</h1>
                        {faqData.map((item, index) => (
                            <Accordion
                                key={index}
                                expanded={expanded === `panel${index}`}
                                onChange={handleChange(`panel${index}`)}
                                className={`${classes.accordion} ${index === 0 && classes.firstAccordion}`}
                            >
                                <AccordionSummary
                                    expandIcon={expanded === `panel${index}` ? <RemoveIcon className={classes.accordionIcon} /> : <AddIcon className={classes.accordionIcon} />}
                                    aria-controls={`panel${index}a-content`}
                                    id={`panel${index}a-header`}
                                    className={classes.accordionSummary}
                                >
                                    <Typography variant='p' className={classes.accordionText}>{item.title}</Typography>
                                </AccordionSummary>
                                <AccordionDetails className={classes.accordionDetails}>
                                    <Typography variant='p' className={classes.accordionText} >{item.content}</Typography>
                                </AccordionDetails>
                            </Accordion>
                        ))}
                    </div>
                </Grid>
            </Grid>
        </Container>
    );
}
