import React, { useState, useEffect, useRef } from "react";
import toast from "react-hot-toast";
import {
  createChatSourceByText,
  createChatbot,
} from "../../../lib/api/ApiCreateChatbot";
import {
  fetchChatbotSource,
  updateChatSource,
} from "../../../lib/api/ApiUpdateChatbot";
import { useParams } from "react-router-dom";
import { useHistory } from "react-router-dom";
// import WarningIcon from '@mui/icons-material/Warning';

export default function Text({ isClear, setIsClear, textList, isEditMode }) {
  const [count, setCount] = useState(0);
  const [isProcessing, setIsProcessing] = useState(false);
  const [serverData, setServerData] = useState(null); // null | undefined | data
  const [isLoaded, setIsLoaded] = useState(false);
  const history = useHistory()

  const { id } = useParams();

  let [text, setText] = useState("");

  useEffect(() => {
    if (textList.length > 0) {
      loadDefault(textList[0].id, textList[0].sourceId);
    } else {
      setIsLoaded(true);
    }
  }, []);

  async function loadDefault(id, sourceId) {
    const res = await fetchChatbotSource(id, "source", sourceId);

    if (res.data) {
      if (res.data.chatbot_source.length > 0) {
        setServerData(res.data.chatbot_source[0]);
        setText(res.data.chatbot_source[0].source_data);
        setCount(res.data.chatbot_source[0].source_data.length);
      } else {
        toast.error("No default data exist");
      }
    } else {
      toast.error(res.data);
    }

    setIsLoaded(true);
  }

  async function getChatbotId() {
    const res = await createChatbot();
    if (res.data) {
      // toast.success('id created')
      return res.data.chatbot_id;
    } else {
      toast.error(res.message);
      return null;
    }
  }

  async function onSubmit() {
    if (isProcessing) return;
    if (text.length == 0) {
      toast("Please write something first!", {
        icon: "⚠",
      });
      return;
    }

    // get chatbot id
    setIsProcessing(true);

    if (isEditMode) {
      if (serverData) {
        const res = await updateChatSource(
          serverData.chat_model_id,
          serverData.chat_bot_source_detail_id,
          text,
          "text"
        );
        if (res.data) {
          toast.success("Successfully updated");
        } else {
          toast.error(res.message);
        }
      } else {
        await createChatSource(id);
      }
    } else {
      const chatbotId = await getChatbotId();
      if (!chatbotId) {
        setIsProcessing(false);
        return;
      }

      await createChatSource(chatbotId);
    }

    setIsProcessing(false);
  }

  async function createChatSource(chatbotId) {
    // sending data to server
    const res = await createChatSourceByText(chatbotId, text);
    if (res.data) {
      toast.success("Chatbot created successfully");
      history.goBack()
    } else {
      toast.error(res.message);
    }
  }

  function onTyping(value) {
    setText(value);
    setCount(value.length);
  }

  useEffect(() => {
    if (isClear == false) return;
    // setQuestions([""]);
    // setAnswers([""]);
    setText("");
    setCount(0);

    setIsClear(false);
  }, [isClear]);

  return (
    <div>
      {isLoaded ? (
        <div>
          <textarea
            value={text}
            maxLength={400_000}
            onChange={(e) => onTyping(e.target.value)}
            className="hide-border"
            rows={20}
            placeholder="Write here something..."
          ></textarea>
          <hr />
          <div className="limit-container">
            <p>
              Total detected characters : <span>{count}</span>/400,000 limit
            </p>
            <button onClick={onSubmit} className="hover">
              {isProcessing == true ? (
                <span className="loading"></span>
              ) : (
                <>{isEditMode ? "Retrain Chatbot" : "Create Chat"}</>
              )}
            </button>
          </div>
        </div>
      ) : (
        <>
          <div className="text skeleton"></div>
          <div className="text skeleton"></div>
          <div className="text skeleton"></div>
        </>
      )}
    </div>
  );
}
