import { withStyles } from '@material-ui/core/styles';  // Import withStyles from "@material-ui/core/styles"
import TextField from '@material-ui/core/TextField';  // Import TextField from "@material-ui/core"

const CustomTextField = withStyles({
  root: {
    '& label.Mui-focused': {
      color: '#FFFFFF', // Text color when focused
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: '#FFFFFF', // Border color when focused
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: '#FFFFFF', // Border color in its normal state
      },
    },
    '& input': {
      color: '#FFFFFF', // Text color
    },
    '&:hover': {
      '& label.Mui-focused': {
        color: '#FFFFFF', // Text color on hover
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: '#FFFFFF', // Border color on hover
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: '#FFFFFF', // Border color on hover
        },
      },
    },
  },
})(TextField);

export default CustomTextField;