import { Grid, makeStyles } from '@material-ui/core'
import React from 'react'
import SubPlanComps from '../Utils/SubPlanComps'
import * as fields from '../Utils/AllFields'

const useStyles = makeStyles((theme) => (
    {
        SubPlanHead: {
            fontSize: '60px',
            fontWeight: "500",
            lineHeight: '64px',
            color: '#FFFFFF'
        }
    }
))

function SubPlans() {
    const classes = useStyles()
    const subPlans = fields.subPlansforYou
    return (
        <>
            <Grid container>
                <Grid item xs={12} className={classes.SubPlanHead}>
                    Sub Plans For You
                </Grid>
            </Grid>
            {
                subPlans.map((sub) => (
                    <SubPlanComps
                        txt1={sub.txt1}
                        txt2={sub.txt2}
                        price={sub.price}
                        input={sub.input}
                    />
                ))
            }
        </>
    )
}

export default SubPlans