import * as React from 'react';
import Card from '@material-ui/core/Card'; 
import CardContent from '@material-ui/core/CardContent'; 
import Typography from '@material-ui/core/Typography';  
import { makeStyles } from '@material-ui/core/styles';  
import right_side_icon from '../assets/right_side_icon.png';
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";

const useStyles = makeStyles((theme) => ({
  card: {
    padding: theme.spacing(4),
    display: 'flex',
    flexDirection: 'column',
    minHeight: '200px',
    [theme.breakpoints.down("sm")]: {
      width: '230px',
      height: '79px',
    },
    cursor:"pointer"
  },
  button: {
    marginTop: theme.spacing(2),
    backgroundColor: 'transparent',
    textAlign: 'left',
    justifyContent: 'left'
  },
  cardMedia: {
    maxHeight: '50',
    minWidth: '150'
  },
  customIcon: {
    width: '64px',
    height: '48px',
    [theme.breakpoints.down('lg')]: {
      width: '64px',
    },
    [theme.breakpoints.between("sm", "md")]: {
      width: '60px',
    },
    [theme.breakpoints.down("sm")]: {
      width: '54px',
      height: '40px',
    },
    cursor: 'pointer !important'
  },
  descriptionText: {
    fontSize: '20px',
    fontWeight: 600,
    lineHeight: '23px',
    letterSpacing: '0.02em',
    color: '#FFFFFF',
  }
}));

const MediaCard = (props) => {
  const { title, description, backgroundColor, width } = props;
  const classes = useStyles();
 
  const history = useHistory()
  
  const handleIntegratesRedirection = () =>{
    if(title === "Shopify"){
      history.push('/integrates/shopify')
    }
    else if(title === "fB"){
      history.push('/integrates/facebook')
    }
    else if(title === "WhatsApp"){
      history.push('/integrates/whatsapp')
    }
  }

  return (
    <Card className={classes.card} style={{ backgroundColor: backgroundColor }}>
      <img
        src={props.image}
        alt={title}
        className={classes.cardImage}
        height={50}
        width={width}
      />
      <CardContent style={{padding:'20px 0 16px 0'}}>
        <Typography variant="p" className={classes.descriptionText}>
          {description}
        </Typography>
      </CardContent>
      <img src={right_side_icon} className={classes.customIcon} alt='icon' onClick={handleIntegratesRedirection} />
    </Card>
  );
};

export default MediaCard;
