import React from 'react';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
  assistantContainer: {
    position: 'self-start',
    maxWidth: '38rem',
    display:'inline-block',
    padding: theme.spacing(1),
    backgroundColor: theme.palette.common.white,
    borderRadius: '10px',
    height: 'auto', 
    '& .text': {
      color: '#0F0F16',
      fontSize: 'lg',
      padding: theme.spacing(1),
    },
  },
  userContainer: {
    alignSelf: 'flex-end',
    maxWidth: '38rem',
    padding: theme.spacing(1),
    backgroundColor: '#036666',
    borderRadius: '10px',
    justifyContent: 'center',
    alignItems: 'center',
    display: 'flex',
    flexDirection: 'column',
    height: 'auto',
    '& .text': {
      color: '#EAEAEC',
      fontSize: 'lg',
      padding: theme.spacing(1),
    },
  }
}));

const Box = ({ text, role }) => {
  const classes = useStyles();
  
  return (
    <div className={role === 'assistant' ? classes.assistantContainer : classes.userContainer}>
      <div className="text">{text}</div>
    </div>
  );
};

export default Box;