import { Button, Grid, Paper, makeStyles } from '@material-ui/core'
import React from 'react'
import sp_logo from '../assets/sp_logo.png';
import wa_logo from '../assets/wa_logo.png';
import wp_logo from '../assets/wp_logo.png';
import FB_messenger from '../assets/FB_messenger.png';
import MyProfileImage from '../assets/MyProfileImage.png'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';

const useStyles = makeStyles((theme) => ({
    profileMainContainer: {
        backgroundColor: "var(--primarycolours-800, #0F0F16)",
        paddingBottom: '100px'
    },
    profileMain: {
        [theme.breakpoints.up('sm')]: {
            padding: '0 8.15%',
        },
        [theme.breakpoints.down('sm')]: {
            padding: '0 4.15%',
        },
        fontFamily: 'Raleway Bold',
        color: '#FFFFFF',
    },
    profileMainComponents: {
        marginTop: '30px',
        flexGrow: 1
    },
    profilepaper: {
        padding: theme.spacing(2),
    },
    profileBackgrounded: {
        background: '#181924',
        padding: '38px'
    },
    profileButtons: {
        background: 'rgba(85, 87, 104, 1)',
        padding: '21px',
        textAlign: 'center',
    },
    profileDeleteButton: {
        background: 'rgba(255, 0, 0, 0.16)',
        padding: '21px',
        textAlign: 'center',
        marginTop: '20px',
    },
    profileButton: {
        fontFamily: 'Raleway Bold',
        fontSize: "24px",
        fontWeight: 600,
        lineHeight: "24px",
        color: '#FFFFFF',
        textTransform: 'none',
        [theme.breakpoints.down('sm')]: {
            fontSize: "18px",
            fontWeight: 500,
            lineHeight: "20px",
        },
    },
    profileMainHead: {
        fontWeight: 600,
        fontSize: '60px',
        lineHeight: '60px',
        [theme.breakpoints.down('sm')]: {
            fontSize: '24px',
            lineHeight: '26px',
        },
    },
    profileSubHead: {
        fontWeight: 600,
        fontSize: '48px',
        lineHeight: '48px',
        [theme.breakpoints.down('sm')]: {
            fontSize: '20px',
            lineHeight: '20px',
        },
    },
    profileSubText: {
        fontWeight: 500, fontSize: '26px', lineHeight: '20px', color: '#808B9E',
        [theme.breakpoints.down('sm')]: {
            fontSize: '16px',
            lineHeight: '24px',
        },
    },
    profileDeleteBtnColor: {
        color: 'rgba(255, 0, 0, 1)'
    },
    profileMainToolsContainer: {
        fontSize: "24px",
        fontWeight: 500,
        lineHeight: "26px"
    },
    profileIntegratesContainer: {
        flexGrow: 1,
    },
    profileIntegratesPaperLogo: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: "#FFFFFF",
        fontSize: "24px",
        fontWeight: 500,
        lineHeight: "26px",
        [theme.breakpoints.down('sm')]: {
            fontSize: '14px',
            lineHeight: '16px',
        },
    },
    profileIntegratesPaper: {
        padding: theme.spacing(3.1),
        textAlign: 'center',
        color: "#FFFFFF",
        fontSize: "24px",
        fontWeight: 500,
        lineHeight: "26px",
        [theme.breakpoints.down('sm')]: {
            fontSize: '14px',
            lineHeight: '16px',
        },
    },
    profileLOGOS: {
        height: '40px',
        [theme.breakpoints.down('sm')]: {
            height: '24px',
        },
    },
    profileWpLogo: {
        width: '176px',
        [theme.breakpoints.down('sm')]: {
            width: '106px',
        },
    },
    profileSpLogo: {
        width: '140px',
        [theme.breakpoints.down('sm')]: {
            width: '84px',
        },
    },
    profileWaLogo: {
        width: '135px',
        [theme.breakpoints.down('sm')]: {
            width: '82px',
        },
    },
    profileFbLogo: {
        width: '258px',
        [theme.breakpoints.down('sm')]: {
            width: '155px',
        },
    },
    profilePlanHead: {
        textAlign: 'center',
        padding: '30px',
        fontSize: '24px',
        lineHeight: '26px',
        fontWeight: 600,
        textTransform: 'uppercase',
        [theme.breakpoints.down('sm')]: {
            fontSize: '14px',
            lineHeight: '16px',
        },
    },
    profilePlanSubContainer: {
        display: 'flex',
        flexDirection: 'column',
        rowGap: '60px'
    },
    profilePlanSubContainerButton: {
        background: '#05668D',
        borderRadius: '10px',
        padding: '25px'
    },
    profilePlanSubContainerElement: {
        fontSize: '34px',
        lineHeight: '36px',
        fontWeight: 600,
        [theme.breakpoints.up('sm')]: {
            paddingLeft: '25px',
        },
        [theme.breakpoints.down('sm')]: {
            fontSize: '18px',
            lineHeight: '20px',
        }
    },
    profilePlanSubContainerItems: {
        color: "#808B9E",
        fontSize: '20px',
        lineHeight: '20px',
        fontWeight: 500,
    },
    profilePlanSubContainerItemsSub: {
        [theme.breakpoints.up('sm')]: {
            paddingLeft: '25px'
        },
        [theme.breakpoints.down('sm')]: {
            fontSize: '14px',
            lineHeight: '16px',
        }
    },
    profilePlanSubContainerlistItems: {
        [theme.breakpoints.down('sm')]: {
            fontSize: '14px',
            lineHeight: '16px',
        }
    },
    profileFirstSection: {
        display: 'flex',
        flexDirection: 'row',
        [theme.breakpoints.down('sm')]: {
            flexDirection: 'column',
        }
    },
    profileImageeContainer: {
        [theme.breakpoints.down('sm')]: {
            display: 'flex',
            justifyContent: 'center'
        }
    },
    profileImagee: {
        width: '324px',
        height: 'auto',
        [theme.breakpoints.down('sm')]: {
            width: '279px',
            display: 'flex',
            justifyContent: 'center'
        }
    },
    profileFirstSectionText: {
        display: 'flex',
        flexDirection: 'column',
        paddingLeft: '60px',
        [theme.breakpoints.down('sm')]: {
            justifyContent: 'center',
            alignItems:'center'
        }
    },
    profileFirstSectionTextName: {
        fontSize: '48px',
        lineHeight: '50px',
        fontWeight: 600,
        [theme.breakpoints.down('sm')]: {
            fontSize: '28px',
        lineHeight: '30px',
        }
    },
    profileFirstSectionTextEmail: {
        fontSize: '30px',
        lineHeight: '30px',
        fontWeight: 500,
        color: '#808B9E',
        [theme.breakpoints.down('sm')]: {
            fontSize: '16px',
        lineHeight: '16px',
        }
    },
    profileFirstSectionTextSubscribedImage: {
        marginTop: '30px'
    },
    profileFirstSectionTextButton: {
        marginTop: '20px',
        background: '#FFFFFF',
        color: theme.palette.common.black,
        fontSize: '16px',
        lineHeight: '20px',
        borderRadius: '15px',
        width: '182px',
        height: '32px',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    }
}))

function MyProfile() {
    const classes = useStyles()
    const history = useHistory()
    const handleLogout = () =>{
        history.push('/')
    }
    return (
        <div className={classes.profileMainContainer}>
            <Grid container className={classes.profileMain}>
                <Grid item xs={12} className={classes.profileMainComponents}>
                    <span className={classes.profileMainHead}>
                        Profile Account
                    </span>
                </Grid>
                <Grid item xs={12} className={`${classes.profileMainComponents} ${classes.profileBackgrounded}`}>
                    <div className={classes.profileFirstSection}>
                        <div className={classes.profileImageeContainer}>
                            <img src={MyProfileImage} className={classes.profileImagee} />
                        </div>
                        <div className={classes.profileFirstSectionText}>
                            <p className={classes.profileFirstSectionTextName} >Michael William</p>
                            <span className={classes.profileFirstSectionTextEmail}>Michael William9458@outlook.com</span>
                            <div className={classes.profileFirstSectionTextSubscribedImage}>
                                <img src={sp_logo} height={24} width={100} />
                                <img src={wp_logo} height={24} width={100} style={{ marginLeft: '30px' }} />
                            </div>
                            <span className={classes.profileFirstSectionTextButton}>04 Chatbot Created</span>
                        </div>
                    </div>
                </Grid>
                <Grid item xs={12} className={classes.profileMainComponents}>
                    <div style={{ background: '#2B2D42' }} className={classes.profilePlanHead}>
                        Currently Using Plan
                    </div>
                    <div className={`${classes.profilePlanSubContainer} ${classes.profileBackgrounded}`}>
                        <div style={{ alignSelf: 'flex-start' }}>
                            <p className={classes.profilePlanSubContainerElement}>
                                Free plan
                            </p>
                            <ul className={classes.profilePlanSubContainerItems}>
                                <li style={{ paddingTop: '10px' }} className={classes.profilePlanSubContainerlistItems}>30 Message credits/ Month</li>
                                <li style={{ paddingTop: '10px' }} className={classes.profilePlanSubContainerlistItems}>400,000 Characters / Chatbot</li>
                                <li style={{ paddingTop: '10px' }} className={classes.profilePlanSubContainerlistItems}>Embed on unlimited websites</li>
                                <li style={{ paddingTop: '10px' }} className={classes.profilePlanSubContainerlistItems}>Upload multiple files</li>
                                <li style={{ paddingTop: '10px' }} className={classes.profilePlanSubContainerlistItems}>View conversation history</li>
                                <li style={{ paddingTop: '10px' }} className={classes.profilePlanSubContainerlistItems}>Capture leads</li>
                                <li style={{ paddingTop: '10px' }} className={classes.profilePlanSubContainerlistItems}>Chatbots get deleted after 07 days of in-activity</li>
                            </ul>
                            <p className={classes.profilePlanSubContainerElement} style={{ marginTop: '30px' }}>
                                Sub Plans For You
                            </p>
                            <span className={`${classes.profilePlanSubContainerItems} ${classes.profilePlanSubContainerItemsSub}`}>Extra Messages $ 07 Per 1000 Msg Credits/Month</span>
                        </div>
                        <div className={classes.profilePlanSubContainerButton} style={{ alignSelf: 'flex-end' }}>
                            <Button className={classes.profileButton}>
                                Upgrade Plan
                            </Button>
                        </div>
                    </div>
                </Grid>
                <Grid item xs={12} className={classes.profileMainComponents}>
                    <div className={classes.profileIntegratesContainer}>
                        <Grid container spacing={3}>
                            <Grid item xs={12}>
                                <Paper className={classes.profileIntegratesPaper} style={{ background: '#2B2D42', textTransform: 'uppercase' }}>Connect a Integrates Tools</Paper>
                            </Grid>
                            <Grid item xs={8}>
                                <Paper className={classes.profileIntegratesPaperLogo} style={{ background: '#00749A' }}><img src={wp_logo} className={`${classes.profileLOGOS} ${classes.profileWpLogo}`}></img></Paper>
                            </Grid>
                            <Grid item xs={4}>
                                <Paper className={classes.profileIntegratesPaper} style={{ background: '#8D99AE' }}>Connected</Paper>
                            </Grid>
                            <Grid item xs={8}>
                                <Paper className={classes.profileIntegratesPaperLogo} style={{ background: '#5A863E' }}><img src={sp_logo} className={`${classes.profileLOGOS} ${classes.profileSpLogo}`}></img></Paper>
                            </Grid>
                            <Grid item xs={4}>
                                <Paper className={classes.profileIntegratesPaper} style={{ background: '#8D99AE' }}>Connected</Paper>
                            </Grid>
                            <Grid item xs={8}>
                                <Paper className={classes.profileIntegratesPaperLogo} style={{ background: '#38B647' }}><img src={wa_logo} className={`${classes.profileLOGOS} ${classes.profileWaLogo}`}></img></Paper>
                            </Grid>
                            <Grid item xs={4}>
                                <Paper className={classes.profileIntegratesPaper} style={{ background: '#1F202F' }}>Connect</Paper>
                            </Grid>
                            <Grid item xs={8}>
                                <Paper className={classes.profileIntegratesPaperLogo} style={{ background: '#0063E2' }}><img src={FB_messenger} className={`${classes.profileLOGOS} ${classes.profileFbLogo}`}></img></Paper>
                            </Grid>
                            <Grid item xs={4}>
                                <Paper className={classes.profileIntegratesPaper} style={{ background: '#1F202F' }}>Connect</Paper>
                            </Grid>
                        </Grid>
                    </div>
                </Grid>
                <Grid item xs={12} className={`${classes.profileMainComponents} ${classes.profileBackgrounded}`}>
                    <div className={classes.profileSubHead}>
                        Usage
                    </div>
                    <p className={classes.profileSubText} >Messages consumed : <span style={{ color: '#FFFFFF' }}>2/30.</span></p>
                    <p className={classes.profileSubText} >Your credits renew at the start of every calendar month.</p>
                    <p className={classes.profileSubText} >Next renewal : <span style={{ color: '#FFFFFF' }}>October 01st</span></p>
                </Grid>
                <Grid item xs={12} className={`${classes.profileMainComponents} ${classes.profileBackgrounded}`}>
                    <div className={classes.profileSubHead}>
                        UrbanChat API Key
                    </div>
                    <p className={classes.profileSubText}>Subscription required to get an API key.</p>
                </Grid>
                <Grid item xs={12} className={`${classes.profileMainComponents} ${classes.profileButtons}`}>
                    <Button className={classes.profileButton} onClick={handleLogout} >
                        Logout Now
                    </Button>
                </Grid>
                <Grid item xs={12} className={classes.profileDeleteButton}>
                    <Button className={`${classes.profileButton} ${classes.profileDeleteBtnColor}`} onClick={handleLogout}>
                        Delete My Account
                    </Button>
                </Grid>
            </Grid>
        </div>
    )
}

export default MyProfile