import React, { useState, useEffect, useRef } from "react";
import toast from "react-hot-toast";
import {
  createChatSourceByLink,
  createChatbot,
  verifyLink,
} from "../../../lib/api/ApiCreateChatbot";
import { useHistory } from "react-router-dom";
import { useParams } from "react-router-dom";

export default function WebsiteLink({ isClear, setIsClear, isEditMode }) {
  const [isProcessing, setIsProcessing] = useState(false);
  const [isSitemapProcessing, setIsSitemapProcessing] = useState(false);
  const [isSubmitProcessing, setIsSubmitProcessing] = useState(false);
  const [count, setCount] = useState(0);
  const [collectedData, setCollectedData] = useState([]);
  const [isLinkVerified, setIsLinkVerified] = useState(false);
  const [isSitemapLinkVerified, setIsSitemapLinkVerified] = useState(false);
  const history = useHistory()

  const { id } = useParams()

  const sitemapInput = useRef();
  const linkInput = useRef();

  const limit = 400_000;

  async function getChatbotId() {
    const res = await createChatbot();
    if (res.data) {
      // toast.success('id created')
      return res.data.chatbot_id;
    } else {
      toast.error(res.message);
      return null;
    }
  }

  async function onVerify(url, isSitemap = false) {
    if (isProcessing || isSitemapProcessing) {
      toast.error("Please wait for the progress to be getting completed");
      return;
    }

    if (count > limit) {
      toast.error("Limit reached out");
      return;
    }

    if (url == "") {
      toast.error("url should not be empty!");
      return;
    }

    if (isSitemap) {
      setIsSitemapProcessing(true);
    } else {
      setIsProcessing(true);
    }
    const res = await verifyLink(url, isSitemap);

    console.log(res);
    if (res.data && res.data.response) {
      let tempCount = count;
      let tempData = [];
      for (const item of res.data.response) {
        if (tempCount > limit) {
          break;
        } else {
          tempCount += item.text_length;
          tempData.push(item);
        }
      }

      // merge the data to array

      if (tempData.length > 0) {
        const newData = [...collectedData, ...tempData];
        console.table(newData);
        setCollectedData(newData);
        setCount(tempCount);
        if (isSitemap) {
          setIsSitemapLinkVerified(true);
        } else {
          setIsLinkVerified(true);
        }
      }
    } else {
      toast.error(res.message);
    }

    if (isSitemap) {
      setIsSitemapProcessing(false);
    } else {
      setIsProcessing(false);
    }
  }

  async function onSubmit() {
    if (isProcessing || isSitemapProcessing || isSubmitProcessing) {
      toast.error("Please wait for the progress to be getting completed");
      return;
    }

    if (count == 0) {
      toast.error("Please verify link first");
      return;
    }



    setIsSubmitProcessing(true);
    if (isEditMode) {
      await createChatSource(id, collectedData);
    } else {
      const chatbotId = await getChatbotId();
      if (!chatbotId) {
        setIsProcessing(false);
        return;
      }

      await createChatSource(chatbotId, collectedData);
    }

    setIsSubmitProcessing(false);
  }



  async function createChatSource(id, data) {
    // sending data to server
    const res = await createChatSourceByLink(id, data);
    if (res.data) {

      if (isEditMode) {

        toast.success("Chatbot updated successfully");
      } else {
        toast.success("Chatbot created successfully");

      }
      history.goBack()
    } else {
      toast.error(res.message);
    }
  }

  useEffect(() => {
    if (isClear == false) return;

    if (isProcessing || isSitemapProcessing || isSubmitProcessing) {
      toast.error("Please wait for the progress to be getting completed");
      return;
    }

    setCount(0);
    setCollectedData([]);
    linkInput.current.value = "";
    sitemapInput.current.value = "";
    setIsSitemapLinkVerified(false)
    setIsLinkVerified(false)

    setIsClear(false);
  }, [isClear]);
  return (
    <div>
      <div>
        <h3>Link</h3>
        <div className="link-container">
          <input ref={linkInput} type="text" />

          {isLinkVerified ? (
            <button className="success">Verified</button>
          ) : (
            <button
              onClick={(e) => onVerify(e.target.previousSibling.value)}
              className="hover"
            >
              {isProcessing == true ? (
                <span className="loading dark small"></span>
              ) : (
                "Verify Link"
              )}
            </button>
          )}
        </div>
        <div className="clean-all">
          <p className="note">
            This will crawl all the links starting with the URL (not including
            files on the website).
          </p>
          {/* <button className="hover primary">Add Link</button> */}
        </div>
        <br />

        <h3>Submit Sitemap</h3>
        <div className="link-container">
          <input ref={sitemapInput} type="text" />

          {isSitemapLinkVerified ? (
            <button className="success">Verified</button>
          ) : (
            <button
              onClick={(e) => onVerify(e.target.previousSibling.value, true)}
              className="hover"
            >
              {isSitemapProcessing == true ? (
                <span className="loading dark small"></span>
              ) : (
                "Verify Link"
              )}
            </button>
          )}
          {/* <button
            onClick={(e) => onVerify(e.target.previousSibling.value, true)}
            className="hover"
          >
            {isSitemapProcessing == true ? (
              <span className="loading dark small"></span>
            ) : (
              "Verify Link"
            )}
          </button> */}
        </div>
        <div className="clean-all">
          <p className="note">
            This will crawl all the links starting with the URL (not including
            files on the website).
          </p>
          {/* <button className="hover primary">Add Link</button> */}
        </div>

        <hr />
        <div className="limit-container">
          <p>
            Total detected characters : <span>{count}</span>/400,000 limit
          </p>
          <button onClick={onSubmit} className="hover">
            {isSubmitProcessing == true ? (
              <span className="loading"></span>
            ) : (
              <>{isEditMode ? "Retrain Chatbot" : "Create Chat"}</>
            )}
          </button>
        </div>
      </div>
    </div>
  );
}
