import { Button, Grid, InputAdornment, InputLabel, TextField, Typography, makeStyles } from '@material-ui/core'
import React from 'react'
import { useState } from 'react';
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";

const useStyles = makeStyles((theme) => (
    {
        SubPlanCompsHead: {
            color: '#FFFFFF',
            background: '#1F202F',
            padding: '50px',
            display: 'flex',
            flexDirection: 'row'
        },
        SubPlanComps: {
            gap: '20px',
            color: '#FFFFFF',
            marginTop: '20px'
        },
        SubPlanCompsBtn: {
            background: '#555768',
            padding: '30px',
            textAlign: 'center',
            fontSize: '24px',
            fontWeight: 500,
            lineHeight: '24px',
            color: '#FFFFFF',
            textTransform: 'none',
            transition: 'background 0.3s',
            '&:hover': {
                background: '#333446',
            },
        },
        SubPlanCompsText: {
            fontSize: '28px',
            fontWeight: 500,
            lineHeight: '46px',
        },
        SubPlanCompsTextBtn: {
            display: 'flex',
            justifyContent: 'center',
            flexDirection: 'column',
        },
        alignLeft: {
            alignItems: 'left',
        },
        alignCenter: {
            alignItems: 'center',
        },
        youNeedField: {
            '& .MuiOutlinedInput-root': {
                width: '75%',
                borderRadius: '10px',
                color:'#FFFFFF',
                border: '1px solid #555768',

                '& fieldset': {
                    borderWidth: '2px',
                    borderColor: '#808B9E',
                },
                '&:hover fieldset': {
                    borderColor: '#808B9E',
                },
                '&.Mui-focused fieldset': {
                    borderColor: '#808B9E',
                },
            },
        },
        subinputLabel:{
            marginBottom: '20px',
            color: '#555768',
            fontSize: '20px',
            fontWeight: 400,
            lineHeight: '20px',
        }
    }
))

function SubPlanComps(props) {
    const classes = useStyles()
    const [value, setValue] = useState(props.input);
    const history = useHistory()
    const calculatePrice = () => {
        return 199;
    }

    const price = calculatePrice(value);

    const handleValueChange = (event) => {
        const newValue = event.target.value;
        setValue(newValue);
    };
    const handleSubscribe = () => {
        history.push('/pricing/payment')
    }
    return (
        <>
            <Grid container className={classes.SubPlanComps}>
                <Grid item xs={12} className={classes.SubPlanCompsHead}>
                    <Grid item xs={6} className={classes.SubPlanCompsText}>
                        {props.txt1}<br />
                        {props.txt2}
                    </Grid>
                    <Grid item xs={6} className={
                        props.input
                            ? `${classes.SubPlanCompsTextBtn} ${classes.alignLeft}`
                            : `${classes.SubPlanCompsTextBtn} ${classes.alignCenter}`
                    }
                    >
                        {
                            props.input ?
                                (
                                    <>
                                        <InputLabel className={classes.subinputLabel}>You Need</InputLabel>
                                        <TextField
                                            variant="outlined"
                                            placeholder='XXXX'
                                            fullWidth
                                            className={classes.youNeedField}
                                            value={value}
                                            onChange={handleValueChange}
                                            InputProps={{
                                                endAdornment: (
                                                    <InputAdornment position="start">
                                                        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', paddingRight: '50px' }}>
                                                            <div style={{ borderRight: '1px solid #000', height: '24px', marginRight: '10px' }}></div>
                                                            <Typography variant="body1" style={{ marginLeft: '10px',fontSize:'30px',lineHeight:'26px',color:'#FFFFFF' }}>${props.price}/Month</Typography>
                                                        </div>
                                                    </InputAdornment>
                                                ),
                                            }}
                                        />

                                    </>) :
                                (
                                    <>
                                        <InputLabel className={classes.subinputLabel}>Pricing</InputLabel><br />
                                        <span style={{fontSize:'36px',lineHeight:'26px'}}>$ {props.price}/Month</span>
                                    </>
                                )
                        }
                    </Grid>
                </Grid>
                <Grid item xs={12}>
                    <Button 
                        // onClick={handleSubscribe}
                     className={classes.SubPlanCompsBtn} fullWidth>Subscribe Now</Button>
                </Grid>
            </Grid>
        </>
    )
}

export default SubPlanComps