import React, { useState } from "react";
import "../../styles/CreateChatbot/common.css";
import "../../styles/Dialog.css";
import "../../styles/CreateChatbot/extra-tab-common.css";
import logo from "../../assets/logo.png";
import whatsapp from "../../assets/whatsapp_logo.png";
import wordpress from "../../assets/wordpress_logo.png";
import shopify from "../../assets/shopify_logo.png";
import messenger from "../../assets/facebook_messenger.png";
import WhatsAppDialog from "../Dialog/WhatsAppDialog";

export default function Integrates() {

  const [isWhatsapp, setIsWhatsapp] = useState(false)

  function onWhatsappDialogClose(){
    setIsWhatsapp(false)
  }

  return (
    <>
      <div className="main-content-holder integrates">
        <div className="header">
          <img className="icon" src={logo} alt="logo" />
        </div>

        <div className="content">
          <button className="tab-button">
            <img src={wordpress} />
            <span className="responsive-hide">Add to Wordpress</span>
            <span className="default green">Free</span>
          </button>

          <button className="tab-button">
            <img src={shopify} />

            <span className="responsive-hide">Add to Shopify</span>
            <span className="default blue">Subscription</span>
          </button>

          <button className="tab-button">
            <img src={whatsapp} />
            <span className="responsive-hide">Add to Whatsapp</span>

            <span className="default blue hover" onClick={() => setIsWhatsapp(true)}>Add</span>
          </button>

          <button className="tab-button">
            <img src={messenger} />
            <span className="responsive-hide">Add to Facebook Messenger</span>

            <span className="default">Coming Soon</span>
          </button>
        </div>
      </div>
      <WhatsAppDialog isOpen={isWhatsapp} onDialogClose={onWhatsappDialogClose}/>
    </>
  );
}
