import React, { useContext, useEffect, useRef, useState } from "react";

import Dropdown from "../../Widget/Dropdown";
import Progress from "../../Widget/Slider";
import Toggle from "../../Widget/Toggle";
import ChangeableInput from "../../Widget/ChangeableInput";
import { ServerDataContext } from "../../../pages/MyChatbot/UpdateChatbot";
import toast from "react-hot-toast";
import { updateChatbotById } from "../../../lib/api/ApiUpdateChatbot";
import { useParams } from "react-router-dom";


// restiction_over_time_to_sent_msg  limit_per_user_msgs
export default function Security() {
  const { serverData, setServerData } = useContext(ServerDataContext)
  const [progress, setProgress] = useState(0);
  const [isSaving, setIsSaving] = useState(false)
  const { id } = useParams();
  
  
  function onModelSelect(value) {}

  const formData = useRef({
    restiction_over_time_to_sent_msg: 0,
    limit_per_user_msgs: 0,
  })


  async function onSave() {

    if (isSaving) return
    const data = structuredClone(formData.current)

    // filter
    if (data.restiction_over_time_to_sent_msg == serverData.view[0].restiction_over_time_to_sent_msg) delete data.restiction_over_time_to_sent_msg
    if (data.limit_per_user_msgs == serverData.view[0].limit_per_user_msgs) delete data.limit_per_user_msgs

    if (Object.keys(data).length == 0) {
      toast.error('Please update something first')
      return
    }

    console.log(data)

    setIsSaving(true)
    const res = await updateChatbotById(id, data)
    if (res.data) {
      toast.success('Updated successfully')
      setServerData((old) => {
        const raw = structuredClone(old)
        if (data.restiction_over_time_to_sent_msg) raw.view[0].restiction_over_time_to_sent_msg = data.restiction_over_time_to_sent_msg
        if (data.limit_per_user_msgs) raw.view[0].limit_per_user_msgs = data.limit_per_user_msgs
        
        return raw
      })

    } else {
      toast.error(res.message)
    }

    setIsSaving(false)
  }



  useEffect(() => {

    // set state
    formData.current.restiction_over_time_to_sent_msg = serverData.view[0].restiction_over_time_to_sent_msg
    formData.current.limit_per_user_msgs = serverData.view[0].limit_per_user_msgs


    // update 
    // setTheme(serverData.view[0].bot_theme)

  }, []);

  return (
    <div>
      <div>
        <h2>Security</h2>

        <p className="input-title">Visibility</p>
        <div style={{ maxWidth: 600 }}>
          <Dropdown items={["Private", "Public"]} onSelect={onModelSelect} />
        </div>
        <p className="note">Enter each message in a new line.</p>

        <div
          style={{
            maxWidth: 600,
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          <div>
            <h3 style={{ marginBottom: 12 }}>Widget</h3>
            <p style={{ marginTop: 12 }} className="input-title">
              Only allow Iframe and on specific domains
            </p>
          </div>
          <Toggle isOn={false} />
        </div>

        <p className="input-title">Rate Limiting</p>
        <p className="note">
          Limit the number of messages sent from one device on the iframe and
          chat bubble (this limit will not be applied to you on chatbase.co,
          only on your website for your users to prevent abuse).
        </p>

        <div className="col-2">
          <ChangeableInput label={'Limit to only'}  onValueChange={(e) => formData.current.limit_per_user_msgs = e} value={serverData.view[0].limit_per_user_msgs}/>
          <ChangeableInput label={'Messages every'} onValueChange={(e) => formData.current.restiction_over_time_to_sent_msg = e} value={serverData.view[0].restiction_over_time_to_sent_msg} extension="Sec"/>
        </div>

        <p className="input-title">Show this message to show when limit is hit</p>
        <div style={{ maxWidth: 600 }}>
          <Dropdown items={["Private"]} onSelect={onModelSelect} />
        </div>

        <div className="button-holder">
          <button className="secondary">Previous</button>
          <button onClick={onSave} className="primary">{isSaving ? (<span className="loading mini"></span>) : "Save & Next"}</button>
        </div>
      </div>
    </div>
  );
}
