import React, { useEffect, useState } from "react";
import {
  createChatSourceByQuestion,
  createChatbot,
} from "../../../lib/api/ApiCreateChatbot";
import { toast } from "react-hot-toast";
import {
  fetchChatbotSource,
  updateChatSource,
} from "../../../lib/api/ApiUpdateChatbot";
import { useParams } from "react-router-dom";
import { useHistory } from "react-router-dom";

export default function Help({ isClear, setIsClear, qaList, isEditMode }) {
  const [count, setCount] = useState([]);
  const [questions, setQuestions] = useState([""]);
  const [answers, setAnswers] = useState([""]);
  const [isProcessing, setIsProcessing] = useState(false);
  const [serverData, setServerData] = useState(null); // null | undefined | data
  const [isLoaded, setIsLoaded] = useState(false)

  const history = useHistory()

  const { id } = useParams();

  useEffect(() => {
    if (qaList.length > 0) {
      loadDefault(qaList[0].id, qaList[0].sourceId);
    } else {
      setIsLoaded(true)
    }
  }, []);

  async function loadDefault(id, sourceId) {
    const res = await fetchChatbotSource(id, "source", sourceId);

    if (res.data) {
      // console.log(res.data)
      if (res.data.chatbot_source && res.data.chatbot_source.length > 0) {
        setServerData(res.data.chatbot_source[0]);
        const arr = JSON.parse(res.data.chatbot_source[0].source_data);
        setQuestions([arr[0].question]);
        setAnswers([arr[0].answer]);
      } else {
        toast.error("No default data exist");
      }
    } else {
      toast.error(res.data);
    }

    setIsLoaded(true)
  }

  async function getChatbotId() {
    const res = await createChatbot();
    if (res.data) {
      // toast.success('id created')
      return res.data.chatbot_id;
    } else {
      toast.error(res.message);
      return null;
    }
  }

  async function onSubmit() {
    if (isProcessing) return;
    if (count == 0) {
      toast("Please write something first!", {
        icon: "⚠",
      });
      return;
    }

    setIsProcessing(true);

    // sending data to server
    const arr = [];
    for (let index = 0; index < answers.length; index++) {
      arr.push({ question: questions[index], answer: answers[index] });
    }

    if (isEditMode) {

      if (serverData) {
        const res = await updateChatSource(
          serverData.chat_model_id,
          serverData.chat_bot_source_detail_id,
          arr,
          "qa"
        );
        if (res.data) {
          toast.success("Successfully updated");
        } else {
          toast.error(res.message);
        }
      } else {
        await createChatSource(id, arr);
      }

    } else {
      // get chatbot id
      const chatbotId = await getChatbotId();
      if (!chatbotId) {
        setIsProcessing(false);
        return;
      }

      await createChatSource(chatbotId, arr)

    }

    setIsProcessing(false);
  }


  async function createChatSource(chatbotId, arr) {
    const res = await createChatSourceByQuestion(chatbotId, arr);
    if (res.data) {
      toast.success("Chatbot created successfully");
      history.goBack()
    } else {
      toast.error(res.message);
    }
  }

  function onQuestionTyping(index, text) {
    setQuestions((old) => {
      const arr = old.slice();
      arr[index] = text;
      return arr;
    });
  }

  function onAnswerTyping(index, text) {
    setAnswers((old) => {
      const arr = old.slice();
      arr[index] = text;
      return arr;
    });
  }

  useEffect(() => {
    let tempCount = 0;

    for (const iterator of questions) {
      tempCount += iterator.length;
    }

    for (const iterator of answers) {
      tempCount += iterator.length;
    }

    setCount(tempCount);
  }, [questions, answers]);

  useEffect(() => {
    if (isClear == false) return;
    setQuestions([""]);
    setAnswers([""]);
    setCount(0);

    setIsClear(false);
  }, [isClear]);

  return (
    <div>

      {isLoaded ? (<div>
        <h3>Question</h3>
        <input
          value={questions[0]}
          onChange={(e) => onQuestionTyping(0, e.target.value)}
          placeholder="Write question..."
        />

        <br />
        <br />
        <h3>Answer</h3>
        <textarea
          value={answers[0]}
          onChange={(e) => onAnswerTyping(0, e.target.value)}
          rows={6}
          placeholder="Write here something..."
        ></textarea>
        <hr />
        <div className="limit-container">
          <p>
            Total detected characters : <span>{count}</span>/400,000 limit
          </p>
          <button onClick={onSubmit} className="hover">
            {isProcessing == true ? (
              <span className="loading"></span>
            ) : (
              <>{isEditMode ? "Retrain Chatbot" : "Create Chat"}</>
            )}
          </button>
        </div>
      </div>) : (
        <>
          <div className="text skeleton"></div>
          <div className="text skeleton"></div>
          <div className="text skeleton"></div>
        </>
      )}

    </div>
  );
}
