import React, { useEffect, useState } from "react";

import fileIcon from "../../../assets/Icons/fileIcon.svg";
import textIcon from "../../../assets/Icons/textIcon.svg";
import linkIcon from "../../../assets/Icons/linkIcon.svg";
import helpIcon from "../../../assets/Icons/helpIcon.svg";
import logo from "../../../assets/logo.png";

import "../../../styles/CreateChatbot/common.css";
import "../../../styles/CreateChatbot/extra-tab-common.css";

import Files from "./Files";
import Text from "./Text";
import WebsiteLink from "./WebsiteLink";
import Help from "./Help";
import { createChatbot } from "../../../lib/api/ApiCreateChatbot";
import { fetchChatbotSource } from "../../../lib/api/ApiUpdateChatbot";
import { useParams } from "react-router-dom";
import { toast } from "react-hot-toast";

const tabContent = [
  {
    icon: fileIcon,
    text: "Files",
  },
  {
    icon: textIcon,
    text: "Text",
  },
  {
    icon: linkIcon,
    text: "Website Link",
  },
  {
    icon: helpIcon,
    text: "Q & A",
  },
];

export default function Sources({ isOutside }) {
  const [activeTab, setActiveTab] = useState(0);
  const [isClear, setIsClear] = useState(false);
  const [isLoaded, setIsLoaded] = useState(false);

  const [qaList, setQaList] = useState([]);
  const [textList, setTextList] = useState([]);

  const { id } = useParams();

  function clear() {
    setIsClear(true);
  }

  useEffect(() => {
    loadChatSource();
  }, []);

  async function loadChatSource() {
    if (isOutside) return;

    const res = await fetchChatbotSource(id, "detail");
    if (res.data) {

      const tempQaList = []
      const tempTextList = []
      res.data.chatbot_sources_details.forEach((element) => {
        if (element.type == "qa") {
          tempQaList.push({ id: element.chat_model_id, count: element.num_of_characters, sourceId: element._id })
        } else if (element.type == "text") {
          tempTextList.push({ id: element.chat_model_id, count: element.num_of_characters, sourceId: element._id })
        }
      });


      setQaList(tempQaList)
      setTextList(tempTextList)

      setIsLoaded(true);
    } else {
      toast.error(res.message);
    }
    console.log(res);
  }

  return (
    <>
      {isOutside == true && (
        <div className="tab-outside hide-scroll">
          {tabContent.map((value, index) => {
            return (
              <div
                key={index}
                onClick={() => setActiveTab(index)}
                className={activeTab == index ? "tab active" : "tab"}
              >
                <img src={value.icon} />
                {value.text}
              </div>
            );
          })}
        </div>
      )}

      <div className="main-content-holder" style={{ paddingBottom: 0 }}>
        <div className="header hide-scroll">
          {isOutside == false &&
            tabContent.map((value, index) => {
              return (
                <div
                  key={index}
                  onClick={() => setActiveTab(index)}
                  className={activeTab == index ? "tab active" : "tab"}
                >
                  <img src={value.icon} />
                  {value.text}
                </div>
              );
            })}

          {isOutside == true && (
            <>
              <img className="icon" src={logo} alt="logo" />
              <button className="reset-btn hover" onClick={clear}>
                Reset
              </button>
            </>
          )}
        </div>

        <div className="content files">
          {activeTab == 0 && (
            <Files isClear={isClear} setIsClear={setIsClear} isEditMode={!isOutside} />
          )}
          {activeTab == 1 && <Text isClear={isClear} setIsClear={setIsClear} textList={textList} isEditMode={!isOutside} />}
          {activeTab == 2 && (
            <WebsiteLink isClear={isClear} setIsClear={setIsClear} isEditMode={!isOutside} />
          )}
          {activeTab == 3 && <Help isClear={isClear} setIsClear={setIsClear} qaList={qaList} isEditMode={!isOutside} />}
        </div>
      </div>
    </>
  );
}
