import React from 'react';
import { Container, Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import sp_logo from '../../assets/sp_logo.png';
import wa_logo from '../../assets/wa_logo.png';
import wp_logo from '../../assets/wp_logo.png';
import fb_logo from '../../assets/fb_logo.png';
import doodle from '../../assets/doodle.png';

import MediaCard from '../../Utils/MediaCard';

const useStyles = makeStyles((theme) => ({
  toolsHead: {
    paddingBottom: '50px',
    backgroundColor: 'var(--primarycolours-900, #0F0F16)',
    color: '#FFFFFF',
    padding: '20px 10%',
  },
  toolsMaintext: {
    fontSize: '60px',
    fontWeight: 500,
    lineHeight: '94px',
    letterSpacing: '0em',
    textAlign: 'center',
    zIndex: 100,
    [theme.breakpoints.between('sm', 'md')]: {
      fontSize: '40px',
      lineHeight: '70px',
    },
    [theme.breakpoints.down('sm')]: {
      fontSize: '17.38px',
      lineHeight: '27.23px',
    },
  },
  toolContainer: {
    padding: '0px 10%',
    [theme.breakpoints.down('md')]: {
      padding: '0px',
    },
  },
  toolsItems: {
    [theme.breakpoints.down('sm')]: {
      display: 'flex',
      justifyContent: 'center',
    },
    // Add custom spacing styles here
    padding: '16px', // Adjust this value to control spacing
  },
  doodle: {
    position: 'absolute',
    top: '50px',
    left: '40%',
    [theme.breakpoints.between('sm', 'md')]: {
      width: '192px',
      height: '96px',
    },
    [theme.breakpoints.down('sm')]: {
      width: '93px',
      height: '41px',
      top: '10px',
    },
  },
  imgTextTools: {
    position: 'relative',
  },
}));

const toolsData = [
  {
    title:'WordPress',
    image: wp_logo,
    description: 'Add an Ai chatbot to your WordPress website',
    backgroundColor: '#00749a',
    iconWidth: '220',
  },
  {
    title:'Shopify',
    image: sp_logo,
    description: 'Add an Ai chatbot to your Shopify website',
    backgroundColor: '#5A863E',
    iconWidth: '175',
  },
  {
    title:'WhatsApp',
    image: wa_logo,
    description: 'Coming Soon',
    backgroundColor: '#38B647',
    iconWidth: '170',
  },
  {
    title:'fB',
    image: fb_logo,
    description: 'Add an Ai chatbot to your Facebook Messenger website',
    backgroundColor: '#0063E2',
    iconWidth: '150',
  },
];

function Tools() {
  const classes = useStyles();

  return (
    <Container maxWidth={false} className={classes.toolsHead}>
      <div className={classes.imgTextTools}>
        <h1 className={classes.toolsMaintext}>UrbanChat integrates with your<br />Favourite tools</h1>
        <img src={doodle} alt='doodle' className={classes.doodle} />
      </div>
      <Grid container spacing={2} className={classes.toolContainer}>
        {toolsData.map((item, index) => (
          <Grid item xs={12} sm={6} key={index} className={classes.toolsItems}>
            <MediaCard
              image={item.image}
              title={item.title}
              description={item.description}
              backgroundColor={item.backgroundColor}
              width={item.iconWidth}
            />
          </Grid>
        ))}
      </Grid>
    </Container>
  );
}

export default Tools;