import React from "react";
import "../../styles/MyChatbot/chatCard.css";
import botIcon from "../../assets/Icons/bot.png";
import fileIcon from "../../assets/Icons/fileIcon.svg";
import helpIcon from "../../assets/Icons/helpIcon.svg";
import textIcon from "../../assets/Icons/textIcon.svg";
import { Link } from "react-router-dom";

export default function ChatCard({ type, title, content, chatbotId }) {
  return (
    <Link to={'/my-chatbot/update/' + chatbotId}>
      <div className="chat-card">
        <div className="content">
          <img src={content} />
        </div>
        <div className="title">
          <div className="icon">
            <img src={botIcon} />
            {/* {type == "link" && <img src={linkIcon} />}
          {type == "file" && }
          {type == "help" && <img src={helpIcon} />}
          {type == "text" && <img src={textIcon} />} */}
          </div>
          {title}
        </div>
      </div>
    </Link>
  );
}
