import { Container, makeStyles } from '@material-ui/core'
import React from 'react'
import ShopifyIntegrates from '../components/ShopifyIntegrates'

const useStyles = makeStyles((theme) => ({
    shopifyToolMain: {
        padding: '60px',
        background: theme.palette.common.black,
        textAlign: 'center',
        [theme.breakpoints.down('sm')]: {
            padding: '15px',
        }
    },
    shopifyToolIntro: {
        padding: '0 10%',
        [theme.breakpoints.down('sm')]: {
            padding: '0',
        }
    },
    shopifyToolIntroHead: {
        color: '#FFFFFF',
        fontSize: '60px',
        fontWeight: 500,
        lineHeight: '94px',
        margin: '0',
        padding: '0 15%',
        [theme.breakpoints.down('sm')]: {
            fontSize: '24px',
            fontWeight: 600,
            lineHeight: '36px',
            padding: '0',
        }
    },
    shopifyToolIntroSubHead: {
        color: '#717280',
        fontSize: '22px',
        fontWeight: 500,
        lineHeight: '38px',
        padding: '0 3%',
        [theme.breakpoints.down('sm')]: {
            fontSize: '12px',
            fontWeight: 400,
            lineHeight: '16px',
            padding: '0'
        }
    }
}))

function ShopifyTools() {
    const classes = useStyles()
    return (
        <Container maxWidth={false} className={classes.shopifyToolMain}>
            <div className={classes.shopifyToolIntro}>
                <p className={classes.shopifyToolIntroHead}>
                    Do more with UrbanChat
                    integrations
                </p>
                <p className={classes.shopifyToolIntroSubHead}>
                    lets you connect UrbanChat with thousands of the most popular Website ,
                    so you can automate your work and have more time for what matters most—no code required.
                </p>
            </div>
            <ShopifyIntegrates />
        </Container>
    )
}

export default ShopifyTools