import React, { useEffect, useState } from "react";
import "../../styles/page.css";
import "../../styles/MyChatbot/index.css";
import addIcon from "../../assets/Icons/addIcon.svg";
import image1 from "../../assets/Rectangle25.png";
import image2 from "../../assets/Rectangle26.png";
import ChatCard from "../../components/MyChatbot/ChatCard";
import toast, { Toaster } from "react-hot-toast";

import { logIn } from "../../lib/api/ApiAuth";
import { Link } from "react-router-dom";
import { getAllMyChatbot } from "../../lib/api/ApiCreateChatbot";
import { useParams } from "react-router-dom";
import { useLocation } from "react-router-dom";


function useQuery() {
  const { search } = useLocation();

  return React.useMemo(() => new URLSearchParams(search), [search]);
}

export default function MyChatbot() {

  let query = useQuery();
  const [isLoaded, setIsLoaded] = useState(false)

  const [allChatbot, setAllChatbot] = useState([])
  useEffect(function () {
    // alert(token)
    if (query.get('token')) {
      localStorage.setItem('access_token', query.get('token'))
    }
    loadChatBots();
  }, []);

  // async function test() {
  //   const res = await logIn("a9649060356@gmail.com", "test1234567");
  //   if (res.data) {
  //     // toast.success("Login successful");
  //     localStorage.setItem("access_token", res.data.token);
  //   } else {
  //     toast.error(res.message);
  //   }
  // }

  async function loadChatBots() {
    const res = await getAllMyChatbot();
    if (res.data) {
      console.log(res.data)

      const arr = res.data.view.map((element) => {
        const field = {
          type: 'bot',
          title: element.name == '' ? element._id : element.name,
          chatbotId: element.chat_model_id,
          content: element.bot_picture == '' ? image1 : element.bot_picture
        }

        return field
      })

      setAllChatbot(arr)
      setIsLoaded(true)

    } else {
      toast.error(res.message);
    }
  }

  return (
    <>
      <section className="card-area">
        <div className="page">
          <div className="header">
            <div>
              <h1>My Chatbots</h1>
              <span>{allChatbot.length} Create Chatbots</span>
            </div>

            <Link to={"/my-chatbot/create"}>
              <button>
                <img src={addIcon} />
                <span className="responsive-hide">New Chatbot</span>
              </button>
            </Link>
          </div>

          <div className="skeleton">
            <p></p>
          </div>


          <div className="content">
            {isLoaded ? (allChatbot.map((value, index) => {
              return (
                <ChatCard key={index}
                  type={value.type}
                  title={value.title}
                  content={value.content}
                  chatbotId={value.chatbotId}
                />
              );
            })) : [0, 1, 2, 3].map((item, index) => <div key={index} className="card skeleton"></div>)}
          </div>

        </div>
      </section>
    </>
  );
}
