import React, { useEffect, useRef } from "react";

import "../../styles/Widget/dropdown.css";

export default function Dropdown({items, onSelect, value }) {
  function onChange(value) {
    if(onSelect) onSelect(value)
  }

  return (
    <div className="simple-dropdown">
      <select defaultValue={value} onChange={(e) => onChange(e.target.value)}>
        {items.map((value, index) =>  <option key={index} value={value}>{value}</option>)}

      </select>
    </div>
  );
}
