import React from 'react';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import google_logo from '../../assets/google-logo.png';
import UrbanApiService from '../../services/UrbanApiService';
import { useHistory } from 'react-router-dom';
import CustomTextField from '../../Utils/CustomTextField';
import { useMediaQuery } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  loginBox: {
    background: theme.palette.common.black,
    color: '#FFFFFF',
    [theme.breakpoints.up('sm')]: {
      padding: '20px 65px 0 65px'
    }
  },
  loginHeading: {
    fontSize: '24px',
    fontWeight: 600,
    lineHeight: '32px',
    letterSpacing: '-0.6000000238418579px',
    textAlign: 'center',
  },
  loginButton: {
    ...theme.loginBtn,
    background: "#555768 !important",
    textTransform: 'none !important',
  },
  loginMain: {
    background: theme.palette.common.black,
    minHeight: 'calc(100vh - 102px)',
  },
  loginText: {
    fontSize: "16px !important",
    fontWeight: "700 !important",
    lineHeight: "24px !important",
    textDecoration: 'none !important',
  },
  loginSubText: {
    fontSize: "16px !important",
    fontWeight: "500 !important",
    lineHeight: "24px !important",
    textDecoration: 'none !important',
    textAlign: 'center',
    color: '#FFFEFE !important',
    cursor:'pointer'
  },
  loginTerms: {
    textAlign: 'center',
    [theme.breakpoints.up('sm')]: {
      padding: '5px 2em',
    },
  },
  inputField: {
    width: '100%', // Adjust the width of the input fields
  },

  loginButton: {
    ...theme.loginBtn,
    background: "#555768 !important",
    textTransform: 'none !important',
    width: '100%', // Adjust the width of the buttons
  },
}));

export default function SignUpPage() {
  const classes = useStyles();
  const history = useHistory();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'))
  const handlePush = (e) => {
    history.push(e);
  }

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    if (data.get('password') === data.get('confirmPassword')) {
      let payload = {
        "email": data.get('email'),
        "password": data.get('confirmPassword')
      }
      if (payload.email && payload.password) {
        UrbanApiService.signUpCall(payload).then((response) => {
          alert(response.data.message);
        }).catch((ex) => {
          console.log(ex, 'issue');
        })
      }
    }
  };

  return (
       <Grid container className={classes.loginMain}>
      <Grid item xs={12} md={6}>
        <Container component="main" maxWidth="sm">
          <CssBaseline />
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
            }}
            className={classes.loginBox}
          >
            <span className={`${classes.loginHeading} ${classes.loginTerms}`}>
              Unlock Your Path to Boosted Sales - <span style={{ color: "#AA07E0" }}>Start for Free!</span>
            </span>
            <Box component="form" onSubmit={handleSubmit} noValidate sx={{ marginTop: "0.5em" }}>
              <CustomTextField
                className={classes.inputField}
                margin="normal"
                variant="outlined"
                required
                fullWidth
                id="email"
                label="Email Address"
                placeholder='name@email.com'
                name="email"
                autoComplete="email"
                autoFocus
                InputProps={{
                  style: {
                    color: '#FFFFFF',
                    borderColor: '#FF0000',
                    '&:focus': {
                      borderColor: '#FF0000',
                    },
                  },
                }}
                InputLabelProps={{ style: { color: '#FFFFFF' } }}
              />
              <CustomTextField
                className={classes.inputField}
                margin="normal"
                variant="outlined"
                required
                fullWidth
                name="password"
                label="Password"
                placeholder='Password'
                type="password"
                id="password"
                autoComplete="current-password"
                InputProps={{
                  style: {
                    color: '#FFFFFF',
                    borderColor: '#FF0000',
                    '&:focus': {
                      borderColor: '#FF0000',
                    },
                  },
                }}
                InputLabelProps={{ style: { color: '#FFFFFF' } }}
              />
              <CustomTextField
                className={classes.inputField}
                margin="normal"
                variant="outlined"
                required
                fullWidth
                name="confirmPassword"
                label="Confirm Password"
                placeholder='Password'
                type="password"
                id="confirmPassword"
                autoComplete="current-reenterpassword"
                InputProps={{
                  style: {
                    color: '#FFFFFF',
                    borderColor: '#FF0000',
                    '&:focus': {
                      borderColor: '#FF0000',
                    },
                  },
                }}
                InputLabelProps={{ style: { color: '#FFFFFF' } }}
              />
              <Button
                type="submit"
                fullWidth
                variant="contained"
                className={classes.loginButton}
                style={{ marginTop: '1em', marginBottom: '1em' }}
              >
                Sign up
              </Button>
              <Link variant="body2" className={classes.loginSubText} style={{ display: 'flex', justifyContent: 'center', padding: '0.5em 0' }}>
                Already have an account? <span style={{ color: '#FFFFFF', fontWeight: 700, paddingLeft: '10px' }} onClick={() => handlePush('/login')}>Sign in</span>
              </Link>
              <Grid container style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', marginTop: '1em' }}>
                <Grid item>
                  <hr style={{ minWidth:!isMobile? '8em':'4em', border: 'none', borderBottom: '2px solid', marginRight: '1em' }} />
                </Grid>
                <Grid item>
                  OR CONTINUE WITH
                </Grid>
                <Grid item>
                  <hr style={{ minWidth:!isMobile? '8em':'4em', border: 'none', borderBottom: '2px solid', marginLeft: '1em' }} />
                </Grid>
              </Grid>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                className={classes.loginButton}
                style={{ marginTop: '1em', marginBottom: '1em' }}
                onClick={()=>window.location.href='https://urbanchat.ai/api/auth/google'}
              >
                <img src={google_logo} alt='logo' width={28} height={24} />
                Google
              </Button>
            </Box>
            <div className={classes.loginTerms}>
              By continuing, you agree to our Terms of Service and Privacy Policy.
            </div>
          </Box>
        </Container>
      </Grid>
      <Grid item xs={12} md={6}></Grid>
    </Grid>
  );
}