import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import "./index.css";

import { Helmet } from "react-helmet";

ReactDOM.render(
  <>
    <Helmet>
      <link
        href="https://fonts.googleapis.com/css?family=Montserrat"
        rel="stylesheet"
      />
    </Helmet>
    <App />
  </>,
  document.getElementById("root")
);
