import React, { useState } from "react";

import Dropdown from "../../Widget/Dropdown";

export default function ChatHistory() {
  return (
    <div>
      <div>
        <h2>Chat History</h2>

        <div className="flex-wrap">
          <div>
            <p className="input-title">Chatbot Model</p>
            <Dropdown items={["29 Sep 2023  To  29 Oct 2023"]} />
          </div>

          <div>
            <p className="input-title">Confidence Score</p>
            <Dropdown items={["Less than 2.5"]} />
          </div>

          <div>
            <p className="input-title">Sources</p>
            <Dropdown items={["API"]} />
          </div>
        </div>

        <div className="flex-wrap">
          <button className="gray hover">Download History</button>
          <button className="hover">View History</button>
        </div>

        <br />
        <h2>History</h2>
        <div className="chat-area">
          <div className="chat">
            <p>👋 Hi...! I am ChatbaseAI, ask me anything about Chatbase!</p>
          </div>
          <div className="chat">
            <p>
              By the way, you can create a chatbot like me for your website! 😮
            </p>
          </div>
          <div className="chat">
            <p>
              {" "}
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Eveniet,
              architecto quidem velit nemo officiis, hic veniam obcaecati ut
              distinctio odit, cupiditate eaque? Commodi, minus corporis eos
              nobis iste nulla velit?
            </p>
          </div>
          <div className="chat primary">
            <p>Hello, UrbanChat.Ai</p>
          </div>

          <div className="chat">
            <p>
              I'm sorry if there was any misunderstanding, but I'm here to
              provide information and assistance related to Chatbase. If you
              have any questions or need help with Chatbase, feel free to ask.
            </p>
          </div>

          <div className="chat">
            <p>
              To add data to your chatbot, you have a few options. You can
              upload documents in formats like .pdf, .txt, .doc, or .docx.
              Alternatively, you can paste text directly into Chatbase or
              provide a link to your website. Chatbase will extract the data
              from these sources and use it to train the chatbot. Simply choose
              the method that is most convenient for you and let Chatbase handle
              the rest!
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
