import React, { useEffect, useState } from "react";
import { ReactSVG } from "react-svg";
import ReactLoading from "react-loading";
import urbanchatLogo from "../../assets/urbanchatLogo.svg";
import arrowReloadIcon from "../../assets/arrowReloadIcon.png";
import sendIcon from "../../assets/sendicon.svg";
import { makeStyles } from '@material-ui/styles';
import ChatBox from "./ChatBox";
import { TextareaAutosize } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
    chatContainer: {
        position: 'relative',
        margin: '0 auto',
        display: 'flex',
        flexDirection: 'column',
        width: '80%',
        backgroundColor: '#1F202F',
        [theme.breakpoints.down('sm')]: {
            width: '100%',
        }
    },
    demoheader: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        width: '100%',
        height: '65px',
        backgroundColor: '#2B2D42',
    },
    demoheaderchild1: {
        marginLeft: '40px'
    },
    demoheaderchild2: {
        marginRight: '40px'
    },
    reloadIcon: {
        position: 'relative',
        cursor: 'pointer',
    },
    suggestionContainer: {
        position: 'relative',
        width: '100%',
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center',
        paddingTop: '16px',
    },
    divider: {
        position: 'absolute',
        top: '0',
        width: '95%',
        height: '0',
        border: '1px solid rgb(43, 45, 66)',
    },
    suggestionItem: {
        cursor: 'pointer',
        display: 'inline-flex',
        alignItems: 'center',
        justifyContent: 'center',
        padding: '15px 16px',
        borderRadius: '10px',
        gap: '2.5px',
        color: 'white',
        backgroundColor: '#27293C',
        margin: '0 10px'
    },
    promptContainer: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        padding: '6px 20px',
        border: '3px solid #2B2D42',
        backgroundColor: '#181924',
        borderRadius: '10px',
        minWidth: '320px',
        margin: '15px 20%',
        cursor: 'pointer',
        [theme.breakpoints.down('sm')]: {
            margin: 0,
            minWidth: '0'
        }
    },
    promptInput: {
        flexGrow: 1,
        color: '#FFFFFF',
        fontSize: '2xl',
        lineHeight: 'normal',
        background: 'inherit',
        outline: 'none',
        focus: 'none',
        border: 'none',
        fontSize: '18px',
        lineHeight: '24px',
        minHeight: '30px',
        maxHeight: '50px',
        overflowY: 'auto',
        scrollbarWidth: 'thin',
        scrollbarColor: 'transparent transparent',

        '&::-webkit-scrollbar': {
            width: '6px',
        },
        '&::-webkit-scrollbar-thumb': {
            background: '#2B2D42',
            borderRadius: '3px',
        },
        '&::-webkit-scrollbar-thumb:hover': {
            background: '#3576A8',
        },
    },
    sendIconS: {
        paddingLeft: '20px'
    },
    arrowReloadIcon: {
        width: '54px',
        height: 'auto',
        [theme.breakpoints.down('sm')]: {
            width: '35px',
        }
    },
}));

const suggesstions = [
    {
        question: "why I use this web..?",
        answer:
            "I'm sorry if there was any misunderstanding, but I'm here to provide information",
    },
    {
        question: "How do I add data to my chatbot?",
        answer: "answer",
    },
];

const initalConvo = [
    {
        role: "assistant",
        content: "👋 Hi...! I am UrbanChatAI, ask me anything about UrbanChat!",
    },
    {
        role: "assistant",
        content:
            "By the way, you can create a chatbot like me for your website! 😮",
    },
];

const Chatdemo = () => {
    const classes = useStyles();
    const [chat, setChat] = useState(
        () => JSON.parse(localStorage.getItem("urbanchat--livedemo")) || initalConvo
    );
    const [prompt, setPrompt] = useState("");
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        localStorage.setItem("urbanchat--livedemo", JSON.stringify(chat));
    }, [chat]);

    function handleRefresh() {
        setChat(initalConvo);
    }

    function handleSuggestion(id) {
        if (loading) return;
        setLoading(true)
        if (id === 0) {
            handleExistingPromptSend("why I use this web..?")
        }
        else if (id === 1) {
            handleExistingPromptSend("How do I add data to my chatbot?")
        }
    }

    function handleSendWithEnter(event) {
        if (event.key === "Enter") {
            event.preventDefault();
            handleSend();
        }
    }

    function handleSend() {
        if (prompt === "") return;
        handleExistingPromptSend(prompt)
    }
    function handleExistingPromptSend(prompt) {
        const chatPayload = {
            chatbot_id: "62cb5b53-81e2-4d09-8a41-7b374c830ac7",
            messages: [{ "role": "user", "content": prompt }]
        };

        setChat((prevChat) => [...prevChat, { role: "user", content: prompt }]);
        setLoading(true);
        fetch('https://urbanmalta.com/urbanchat/api/chat-completion', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(chatPayload),
        })
            .then(response => response.json())
            .then(data => {
                const assistantResponse = data.content;
                setChat((prevChat) => [
                    ...prevChat,
                    { role: "assistant", content: assistantResponse },
                ]);
            })
            .catch(error => {
                console.log('Error making API call:', error);
            })
            .finally(() => {
                setLoading(false);
                setPrompt("");
            });
    }
    return (
        <div className={classes.chatContainer}>
            <div className={classes.demoheader}>
                <div className={classes.demoheaderchild1}>
                    <ReactSVG src={urbanchatLogo} className={classes.logo_demo} />
                </div><div className={classes.demoheaderchild2}>
                    <div className={classes.reloadIcon} onClick={handleRefresh}>
                        <img
                            src={arrowReloadIcon}
                            alt="Arrow Reload"
                            className={classes.arrowReloadIcon}
                        />
                    </div>
                </div>
            </div>

            <ChatBox chat={chat} loading={loading} />

            <div className={classes.suggestionContainer}>
                <div className={classes.divider}></div>
                <div className={classes.suggestionItem} onClick={() => handleSuggestion(0)}>
                    why I use this web..?
                </div>
                <div className={classes.suggestionItem} onClick={() => handleSuggestion(1)}>
                    How do I add data to my chatbot?
                </div>
            </div>

            <div className={classes.promptContainer}>
                <TextareaAutosize
                    onChange={(e) => setPrompt(e.target.value)}
                    onKeyPress={handleSendWithEnter}
                    value={prompt}
                    className={classes.promptInput}
                    placeholder="Type Here In your Mind...."
                    required
                />
                {loading ? (
                    <div className="">
                        <ReactLoading
                            type="bubbles"
                            color="white"
                            height={28}
                            width={28}
                        />
                    </div>
                ) : (
                    <ReactSVG
                        className={classes.sendIconS}
                        onClick={handleSend}
                        src={sendIcon}
                    ></ReactSVG>
                )}
            </div>
        </div>
    );
};

export default Chatdemo;