import React from 'react';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField'; // Replace CustomTextField with TextField
import Typography from '@material-ui/core/Typography';
import google_logo from '../../assets/google-logo.png';
import UrbanApiService from '../../services/UrbanApiService';
import { useHistory } from 'react-router-dom';
import CustomTextField from '../../Utils/CustomTextField';
import { useMediaQuery } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  loginBox: {
    background: theme.palette.common.black,
    color: '#FFFFFF',
  },
  loginHeading: {
    fontSize: '24px',
    fontWeight: 600,
    lineHeight: '32px',
    letterSpacing: '-0.6000000238418579px',
    textAlign: 'center',
  },
  loginButton: {
    background: "#555768",
    textTransform: 'none',
  },
  loginMain: {
    background: theme.palette.common.black,
    minHeight: 'calc(100vh - 102px)',
  },
  loginText: {
    fontSize: "16px",
    fontWeight: "700",
    lineHeight: "24px",
    textDecoration: 'none',
  },
  loginSubText: {
    fontSize: "16px",
    fontWeight: "500",
    lineHeight: "24px",
    textDecoration: 'none',
    textAlign: 'center',
    color: '#FFFEFE',
    display:'flex',
    cursor:'pointer'
  },
  loginTerms: {
    textAlign: 'center',
    [theme.breakpoints.up('sm')]: {
      padding: '10px 5em',
    },
  },
}));

export default function LoginPage() {
  const classes = useStyles();
  const history = useHistory();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'))

  const handlePush = (path) => {
    history.push(path);
  }

  const handleSubmit = (event) => {
    event.preventDefault();
    // history.push('/my-profile')
    const data = new FormData(event.currentTarget);
    let payload = {
      "email": data.get('email'),
      "password": data.get('password')
    }
    if (payload.email && payload.password) {
        UrbanApiService.loginCall(payload).then((response) => {
        alert(response.data.message);
        
        console.log("login message: ",response.data);
        history.push(`/my-chatbot?token=${response.data.token}`)

      }).catch((ex) => {
        console.log(ex, 'issue');
      })
    }
  };

  return (
    <Grid container className={classes.loginMain}>
      <Grid item xs={12} md={6}>
        <Container component="main" maxWidth="sm">
          <CssBaseline />
          <Box
            style={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
            }}
            className={classes.loginBox}
          >
            <h4 className={classes.loginHeading}>
              Welcome Back
            </h4>
            <Box component="form" onSubmit={handleSubmit} noValidate style={{ marginTop: '0.5em' }}>
              <CustomTextField
                margin="normal"
                variant="outlined"
                required
                fullWidth
                id="email"
                label="Email Address"
                placeholder="name@email.com"
                name="email"
                autoComplete="email"
                autoFocus
                InputProps={{
                  style: {
                    color: '#FFFFFF',
                    borderColor: '#FF0000',
                  },
                }}
                InputLabelProps={{ style: { color: '#FFFFFF' } }}
              />
              <CustomTextField
                margin="normal"
                required
                variant="outlined"
                fullWidth
                name="password"
                label="Password"
                placeholder="Password"
                type="password"
                id="password"
                autoComplete="current-password"
                InputProps={{
                  style: {
                    color: '#FFFFFF',
                    borderColor: '#FF0000',
                  },
                }}
                InputLabelProps={{ style: { color: '#FFFFFF' } }}
              />
              <Typography variant="body2" style={{ marginTop: '1em' }} className={classes.loginText}>
                Forgot password?
              </Typography>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                className={classes.loginButton}
                style={{ marginTop: '1.5em', marginBottom: '1.5em' }}
              >
                Sign in
              </Button>
              <Link variant="body2" className={`${classes.loginSubText} ${classes.loginTerms}`}>
                Don't have an account? <span style={{ color: '#FFFFFF', fontWeight: 700, paddingLeft: '10px' }} onClick={() => handlePush('/signup')}>Sign Up</span>
              </Link>
              <Grid container style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', marginTop: '1em' }}>
                <Grid item>
                  <hr style={{ minWidth:!isMobile? '8em':'4em', border: 'none', borderBottom: '2px solid', marginRight: '1.5em' }} />
                </Grid>
                <Grid item>
                  OR CONTINUE WITH
                </Grid>
                <Grid item>
                  <hr style={{ minWidth:!isMobile? '8em':'4em', border: 'none', borderBottom: '2px solid', marginLeft: '1.5em' }} />
                </Grid>
              </Grid>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                className={classes.loginButton}
                style={{ marginTop: '1.5em', marginBottom: '1.5em' }}
                onClick={()=>window.location.href='https://urbanchat.ai/api/auth/google'}
              >
                <img src={google_logo} alt="logo" width={28} height={24} />
                Google
              </Button>
            </Box>
            <div className={classes.loginTerms}>
              By continuing, you agree to our Terms of Service and Privacy Policy.
            </div>
          </Box>
        </Container>
      </Grid>
      <Grid item xs={12} md={6}></Grid>
    </Grid>
  );
}